<template>
  <div class="early-wrap">
    <ul class="early-nav">
      <router-link tag="li" to="/earlywarning/suspiciousarticle">可疑物品收发预警</router-link>
      <router-link tag="li" to="/earlywarning/keyarea">重点区域人员预警</router-link>
    </ul>
    <router-view></router-view>
  </div>
</template>

<script type="text/javascript">
export default {
  name: "earlywarning",
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/variable";
@import "../../assets/css/mixin";

.early-wrap {
  .early-nav {
    color: #939fc2;
    font-size: $fz-sm;
    display: flex;
    line-height: 26px;
    font-weight: bold;
    box-sizing: border-box;
    position: relative;
    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 2px;
      position: absolute;
      background-color: #102456;
      left: 0;
      bottom: 0;
    }
    li {
      padding: 0 10px 5px;
      margin-right: 10px;
      cursor: pointer;
      position: relative;
    }
    & li:hover {
      color: #fff;
      /*border-bottom: 2px #fed61e solid;*/
      &:after {
        content: "";
        display: block;
        width: 100%;
        height: 2px;
        position: absolute;
        background-color: #fed61e;
        left: 0;
        bottom: 0;
        z-index: 2;
      }
    }
    .active {
      box-sizing: border-box;
      /*border-bottom: 2px #fed61e solid;*/
      font-size: 20px;
      color: #fff;
      &:after {
        content: "";
        display: block;
        width: 100%;
        height: 2px;
        position: absolute;
        background-color: #fed61e;
        left: 0;
        bottom: 0;
        z-index: 2;
      }
    }
  }
}
</style>



