<template>
  <div>
    <table class="table">
      <thead>
      <tr>
        <td>序号</td>
        <td>类别</td>
        <td>品牌</td>
        <td>购物方式</td>
        <td>数据来源</td>
        <td>物品来源地区</td>
        <td>统计区间</td>
        <td>消费区间</td>
        <td>备注</td>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item,index) in list">
        <td>{{index+1}}</td>
        <td>{{item.lb | isValue}}</td>
        <td>{{item.pp | isValue}}</td>
        <td>{{item.gwfs | isValue}}</td>
        <td>{{item.sjly | isValue}}</td>
        <td>{{item.wplydq | isValue}}</td>
        <td>{{item.djqj | isValue}}</td>
        <td>{{item.xfqj | isValue}}</td>
        <td>{{item.bz | isValue}}</td>
      </tr>
      </tbody>
    </table>
    <div class="pagination-wrap">
      <pagination :currentIndex.sync="currentIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
  </div>
</template>

<script type="text/javascript">
  import pagination from 'components/pagination';
  export default {
    name: 'shopping',
    data(){
      return {
        list:[
          {lb:'化妆品',pp:'雅诗兰黛、兰蔻',gwfs:'网络购物',sjly:'京东商城',wplydq:'北京',djqj:'2017-11-01至2017-11-30',xfqj:'2000:00-4000:00',bz:''},
          {lb:'',pp:'',gwfs:'',sjly:'',wplydq:'',djqj:'',xfqj:'',bz:''},
          {lb:'',pp:'',gwfs:'',sjly:'',wplydq:'',djqj:'',xfqj:'',bz:''},
          {lb:'',pp:'',gwfs:'',sjly:'',wplydq:'',djqj:'',xfqj:'',bz:''},
          {lb:'',pp:'',gwfs:'',sjly:'',wplydq:'',djqj:'',xfqj:'',bz:''},
          {lb:'',pp:'',gwfs:'',sjly:'',wplydq:'',djqj:'',xfqj:'',bz:''},
          {lb:'',pp:'',gwfs:'',sjly:'',wplydq:'',djqj:'',xfqj:'',bz:''},
          {lb:'',pp:'',gwfs:'',sjly:'',wplydq:'',djqj:'',xfqj:'',bz:''},
          {lb:'',pp:'',gwfs:'',sjly:'',wplydq:'',djqj:'',xfqj:'',bz:''},
          {lb:'',pp:'',gwfs:'',sjly:'',wplydq:'',djqj:'',xfqj:'',bz:''},
        ],
        // 两种方式都能返回当前页面
        currentIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 70,  // 必须传
        }
      }
    },
    methods:{
      changePage(val) {
        // 改变页码
        console.log(val)
        console.log(this.currentIndex)
      },
    },
    filters:{
      isValue(val) {
        return (val = val == "" ? "-" : val);
      }
    },
    components: {
      pagination,
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";

  .table{
    width: 100%;
    float: left;
    margin-top: 30px;
    thead{
      color: #8388a4;
      border-top: 1px solid #0d3196;
    }
    tr{
      border-bottom: 1px solid #0d3196;
      line-height: 38px;
      text-align: center;
    }
    tbody>tr:hover{
      background-color: #0d3196;
    }
  }
  .pagination-wrap{
    float: right;
    margin-top:15px;
  }
</style>



