<template>
  <div class="pagination-wrap">
    <div class="page-wrap">
      <el-pagination
        background
        layout="prev, pager, next"
        :total=pageData.total
        :page-size=pageData.pageSize
        @current-change="currentPage"
        :current-page=pageData.curPage
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    name: "pagination",
    data() {
      return {}
    },
    props: {
      pageData: {
        type: Object,
        default: {
          curPage: 1,
          pageSize: 10,
          total: 1
        }
      },
      currentIndex: {
        type: Number,
        default: 1
      }
    },
    methods: {
      currentPage(val) {
        this.$emit('update:currentIndex', val)
        this.$emit('changePage', val)
        // console.log(val)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .pagination-wrap{
   display: flex;
    justify-content: flex-end;
  }
  .page-wrap {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .el-pagination.is-background {
      height: 40px;
      border-radius: 20px;
      background-color: rgba(198, 213, 255, .1);
      padding: 0 15px;
      display: flex;
      align-items: center;
      & /deep/ .btn-prev, & /deep/ .btn-next {
        color: #fff;
        border-radius: 50%;
        background-color: transparent;
        border: 1px #7497c1 solid;
        &.disabled {
          color: #666;
          border: 1px #666 solid;
        }
      }
      & /deep/ .el-pager li {
        height: 30px;
        box-sizing: border-box;
        border-radius: 50%;
        background-color: transparent;
        &.active {
          background-color: rgba(59, 156, 255, .5);
          border: 1px #3b9cff solid;
          color: #fff;
        }
        &:hover {
          background-color: rgba(59, 156, 255, .5);
          border: 1px #3b9cff solid;
          color: #fff;
        }
      }
      & /deep/ .el-pager {
        display: inline-block;
        li {
          color: #fff;
        }
      }
    }

  }


</style>



