<template>
  <div class="goods-wrap">
    <p class="search-info">根据重点区域，可疑人员出现的历史数据分析，预测某一时间段内该可疑人员出现的时间与可能性。</p>
    <div class="table-wrap">
      <table class="table-list">
        <thead>
          <tr>
            <th>序号</th>
            <th>重点区域名称</th>
            <th>基站编号</th>
            <th>开始时间</th>
            <th>截止时间</th>
            <th>预计出现的重点人</th>
            <th>预计出现的非重点人</th>
          </tr>
        </thead>
        <tbody v-for="(item,index) in items">
        <tr>
          <td>{{item.id}}</td>
          <td>{{item.areaName}}</td>
          <td>{{item.number}}</td>
          <td>{{item.startTime}}</td>
          <td>{{item.endTime}}</td>
          <td>{{item.importperson}}</td>
          <td>{{item.person}}</td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="mgt-20">
      <pagination :curIndex.sync="curIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
  </div>
</template>

<script>
import datepicker from "components/datepicker";
import pagination from "components/pagination";
import { earlywarning as api } from "../../api/earlywarning";
export default {
  name: "keyarea",
  data() {
    return {
      // 两种方式都能返回当前页面
      earlyNum: 120,
      curIndex: 1,
      pageData: {
        curPage: 1,
        pageSize: 10,
        total: 0 // 必须传
      },
      items: []
    };
  },
  methods: {
    goSearch() {
      // go search
      // console.log('search')
    },
    changePage(index) {
      this.getList({ pageIndex: index });
    },
    geList(params) {
      if (params && params.pageIndex) {
        this.curIndex = params.pageIndex;
      }
      let param = params || {};
      param.pageIndex = this.curIndex;
      param.pageSize = this.pageData.pageSize;
      api.getList1(params).then(res => {
        if (res.code && res.code === 1) {
          // console.log(res.data.list);
          this.items = res.data.list;
          this.areaNum = res.data.areaNum;
          this.pageData.curPage = this.curIndex;
          this.pageData.total = res.data.total;
        }
      });
    }
  },
  components: {
    pagination,
    datepicker
  },
  mounted() {
    this.geList();
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/variable";
@import "../../assets/css/mixin";

.goods-wrap {
  padding:0 20px;
  .date-picker-wrap {
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .date-picker-left {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      span:nth-child(1) {
        color: #c2cae2;
        font-size: $fz-sm;
        font-weight: bold;
        margin-right: 20px;
      }
      .search {
        font-size: $fz-sm;
        line-height: 28px;
        text-align: center;
        background: linear-gradient(to right, #0095ff, #2e5ffc);
        border-radius: 2px;
        width: 90px;
        height: 28px;
        margin-left: 20px;
        transition: all 0.2s ease-in;
        &:hover {
          cursor: pointer;
          box-shadow: 0 0 2px #00bdff;
        }
      }
    }
    .date-picker-right {
      color: #a3d8ff;
      font-size: $fz-sm;
      cursor: pointer;
      transition: all 0.2s ease-in;
      &:hover {
        color: #fff;
      }
    }
  }
  .check-list {
    width: 100%;
    margin-top: 30px;
  }
  .search-info {
    width: 100%;
    height: 30px;
    line-height: 30px;
    margin-top: 30px;
    border-radius: 2px;
    padding-left: 14px;
    background: linear-gradient(
      to right,
      rgba(8, 39, 135, 0.9),
      rgba(8, 39, 135, 0)
    );
  }
  .table-wrap {
    width: 100%;
    padding: 30px 0;
    .table-list {
      width: 100%;
      tr,
      th {
        height: 40px;
        line-height: 40px;
        box-sizing: border-box;
        text-align: center;
      }
      tr {
        border-bottom: 1px #29328f solid;
        transition: all 0.2s ease-in;
        &:hover {
          background-color: #29328f;
        }
        &:nth-child(1) {
          border-top: 1px #29328f solid;
        }
      }
      thead {
        color: #6b759f;
      }
    }
  }
}
</style>



