<template>
	<div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

	</div>
</template>

<script>
	import {chartMixin} from 'assets/js/common'
	export default {
		name: 'bar',
		mixins: [chartMixin],
		props: {
			eventStateData: {
				type: Object,
				default: () => {}
			}
		},
		data() {
			return {}
		},
		watch: {
			eventStateData: {
				handler: function(val, oldVal) {
					this.initChart()
				},
				deep: true //增加deep 观察对象的子对象变化
			}
		},
		mounted() {
			this.$nextTick(() => {
//				console.log(this.eventStateData)
				this.myChart = this.$echarts.init(this.$refs.chart);
				this.initChart()
			})
		},
		methods: {
			initChart() {
				let option = {
					color: ['#3398DB'],
					tooltip: {
						trigger: 'axis',
						axisPointer: {
							show: true,
							type: 'line',
							snap: true,
							lineStyle: {
								color: '#7AAEE8',
								width: 1,
								type: "dashed"
							}
						},
						backgroundColor:"rgba(255,255,255,0.8)",
						textStyle:{
							color:"#474747",
							fontWeight:'bold'
						},
						formatter:'{a0}: {c0}<br />{a1}: {c1}'
					},
					grid: {
						left: '3%',
						right: '4%',
						bottom: '9%',
						top: "14%",
						containLabel: true
					},
					xAxis: [{
						type: 'category',
						data: this.eventStateData.time,
			            boundaryGap: true,   //坐标轴的留白
			            axisLine: {      //隐藏X轴线
			              show: true,
			              lineStyle: {
			                color: "#0592EA",
			              }
			            },
			            axisTick:{
							show:false //坐标轴刻度
						},
			            axisLabel: {
							textStyle: {
								color: '#fff',
								fontSize: 12,
							},
						},
					}],
					yAxis: {
						type: 'value',
						name: '',
						axisTick:{
							show:false //坐标轴刻度
						},
						axisLine: {
							show: false,
						},
						axisLabel: {
							textStyle: {
								color: '#fff',
								fontSize: 12,
							},
						},
						splitLine: {
							lineStyle: {
								color: '#166DD4',
								type: "dashed",

							}
						},
					},
					legend: {
						data: ["可疑人员", "可疑物品"],
						color: "#099D4F",
						itemWidth: 12,
						itemHeight: 3,
						itemGap: 28,
						icon: "line",
						textStyle: {
							color: "#fff",
							fontSize: 12,
						},
						top:0,
					},
					series: [{
							name: '可疑人员',
							type: 'line',
							showSymbol:false,
							barWidth: '60%',
							smooth: true,
							itemStyle: {
								normal: {
									color: "#E8E212"
								},
								emphasis: {
									opacity: 1
								}
							},
							data: this.eventStateData.suspiciousperson
						},
						{
							name: '可疑物品',
							type: 'line',
							smooth: true,
							showSymbol:false,
							barWidth: '60%',
							itemStyle: {
								normal: {
									color: "#98DBFF"
								},
								emphasis: {
									opacity: 1
								}
							},
							data: this.eventStateData.suspiciousgoods
						},
					]
				}
				this.myChart.on('click', (params) => {
					this.$emit('handleCharts', params.name)
				})
				this.myChart.setOption(option);

			},
      // echats 图表自适应
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



