<template>
  <div class="virtual-portrait clearfix">
    <div class="rea_header">
      <div class="left">
        <div class="portrait">
          <img src="../../../assets/img/portrait.png"/>
        </div>
        <ul>
          <li><span>实名姓名<i>:</i></span>{{smxm}}</li>
          <li><span>手机号<i>:</i></span>{{sjh}}</li>
          <li><span>年龄段<i>:</i></span>{{nld}}</li>
        </ul>
        <div class="forecast">
          <div class="foreLeft">
            <span>预测对应人 :</span>
          </div>
          <div class="foreRight">
            <ul>
              <li>
                <span>张立峰</span>
                <el-progress :percentage="85"></el-progress>
                <i>匹配度</i>
              </li>
              <li><span>张丽红</span>
                <el-progress :percentage="55"></el-progress>
                <i>匹配度</i></li>
              <li><span>张宏科</span>
                <el-progress :percentage="36"></el-progress>
                <i>匹配度</i></li>
            </ul>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="tubiao">
          <radar></radar>
        </div>
      </div>
    </div>

    <div class="rea_tab">
      <ul>
        <router-link tag="li" v-for="menu in menus" :to="{name:menu.name,query:query,params:params}">{{menu.title}}
        </router-link>
      </ul>
      <transition name="fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>
  </div>
</template>

<script type="text/javascript">
  import radar from '../../../components/echarts/radar'
  export default {
    name: 'virtualPortrait',
    data(){
      return {
        smxm: '张德志',
        sjh: '18212345678',
        nld: '21岁-25岁', params: this.$route.params,
        query: this.$route.query,
        menus: [
          {title: '手机通讯', name: 'phone'},
          {title: '网络订票', name: 'booking'},
          {title: '交通出行', name: 'traffic'},
          {title: '物流快递', name: 'logistics'},
          {title: '网络购物', name: 'onlineShopping'},
          {title: '旅店预定', name: 'reservation'},
          {title: '网页邮件', name: 'mail'},
          {title: '生活缴费', name: 'pay'},
          {title: '社会网络', name: 'sociology'},
          {title: '博客', name: 'blog'}
        ]
      }
    },
    components: {
      radar,
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";
  .rea_header{
    width: 100%;
    height: 240px;
    padding-top: 40px;
    float: left;
    box-sizing: border-box;
  .left{
    width: 830px;
    float: left;
  .forecast{
    float: left;
    width: 520px;
    height: 144px;
    padding-top: 12px;
  .foreLeft{
    float: left;
    width: 88px;
    color: #969ebb;
  }
  .foreRight{
    width:432px;
    height: 144px;
    /*background-color: red;*/
    float: left;
  ul{
  li{
    width: 100%;
    height: 50px;
  &>span{
      float: left;
      text-decoration: underline;
      margin-top: 2.5px;
      color: #e9ca2d;
    }
  &>div{
      width: 330px;
      float: left;
      margin-top: 3px;
      margin-left: 5px;
    }
  &>i{
      display: inline-block;
      margin-top: 3px;
    font-size: 14px;
    line-height: 25px;
    }
  }
  }
  }
  }
  &>ul{
      float: left;
      margin-left: 20px;
      width: 175px;
  li{
    margin-bottom: 4px;
    width: 175px;
    line-height: 46px;
  span{
    color: #969ebb;
  }
  i{
    margin: 0 7px;
  }
  }
  }
  }
  .right{
  .tubiao{
    float: right;
    width: 315px;
    margin-left: 10px;
    height: 175px;
    border-left: 1px solid #164bb5;
    margin-top: -15px;
  }
  }
  }
  .rea_tab{
    float: left;
    width: 100%;
  ul{
    float: left;
    width: 100%;
    height: 33px;
    background-color: #053394;
    margin: 0 auto 30px auto;
    padding-left: 45px;
    box-sizing: border-box;
  li{
    float: left;
    width: 107px;
    height: 33px;
    line-height: 33px;
    text-align: center;
    color: #8794bd;
    cursor: pointer;
    /*background-color: red;*/
  }
  li.active,li:hover{
    color: white;
    background-color: #2367e2;
  }
  }
  }

  .portrait{
    width: 110px;
    height: 142px;
    border: 1px solid #127df5;
    border-radius:6%;
    overflow: hidden;
    float: left;
    position: relative;
  img{
    position: relative;
    top: -1px;
    left: -1px;
    width: 117px;
    height: 147px;
  }
  img.fingerprint{
    top: -5px;
    margin-right: 10px;
  }
  p{
    width: 100%;
    height: 22px;
    line-height: 22px;
    text-align: center;
    background-color: #051b50;
    position: absolute;
    bottom: 0;
    border-top: 1px solid #127df5;
  }
  }
  .mar10{
    margin-right: 10px;
  }
</style>



