<template>
  <div ref="mapChart" class="mapChart-wrapper" style="height:380px;width:100%">
  </div>
</template>

<script>
  import {chartMixin} from 'assets/js/common.js';
  export default {
    name: "count-chart",
    mixins: [chartMixin],
    props: {
      echartsData: {
        type: Object,
        default: () => {}
      }
    },
    data() {
      return {}
    },
    watch: {
      echartsData: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.myChart = this.$echarts.init(this.$refs.mapChart);

      this.$nextTick(()=>{
        this.initChart()
      })
    },
    methods: {
      initChart() {
        let option = {
          color: ['#149be2'],
          tooltip: {
            trigger: 'axis',
            backgroundColor: '#ced5e2',
            borderColor: '#fff',
            textStyle: {
              color: '#000'
            },
            axisPointer : {
              type : 'shadow',
              shadowStyle: {
                color: "rgba(255,255,255, 0.1)"
              }
            },
            formatter: function (params) {
              let str = ''
              params.forEach((val,index)=>{
                str += `${val.name}:${val.value}`
              })
              return str
            }
          },
          grid: {
            left: '50',
            right: '20',
            bottom: '40',
            containLabel: true
          },
          xAxis : [
            {
              type : 'category',
              data : this.echartsData.xdata,
              axisLine: {
                lineStyle: {
                  color: '#1e9ce4'
                }
              },
              axisLabel: {
                rotate: 30,
                margin: 20,
                color: '#fff',
                fontSize: 14
              }
            }
          ],
          yAxis : [
            {
              type : 'value',
              name:  '数量',
              nameGap: '15',
              nameTextStyle: {
                color: '#fff'
              },
              splitLine: {
                lineStyle: {
                  color: '#165ba1',
                  type: 'dashed',
                  opacity: 0.7
                }
              },
              axisLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                color: '#fff'
              }
            }
          ],
          series : [
            {
              name:'可疑物品',
              type:'bar',
              barWidth: '12',
              itemStyle: {
                normal: {
                  color: '#1ea3d8',
                  opacity: 0.7,
                  borderWidth: 1,
                  borderColor: '#00aeff'
                },
                emphasis: {
                  opacity: 1
                }
              },
              data:this.echartsData.data
            }
          ]
        }
        this.myChart.setOption(option);
      },
      // echats 图表自适应
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
    },
  }
</script>

<style lang="scss" scoped>

</style>



