<template>
  <div class="con">
    <table class="table">
      <thead>
      <tr>
        <td>序号</td>
        <td>应用名称</td>
        <td>用户名</td>
        <td>密码</td>
        <td>用户姓名</td>
        <td>身份证号</td>
        <td>护照号码</td>
        <td>航空公司</td>
        <td>航班(车次)</td>
        <td>出发时间</td>
        <td>出发城市</td>
        <td>到达时间</td>
        <td>到达城市</td>
        <td>座位号</td>
        <td>乘车时间</td>
        <td>订票时间</td>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item,index) in list">
        <td>{{index+1}}</td>
        <td>{{item.yhmc | isValue}}</td>
        <td>{{item.yhm | isValue}}</td>
        <td>{{item.mm | isValue}}</td>
        <td>{{item.yhxm | isValue}}</td>
        <td>{{item.sfzh | isValue}}</td>
        <td>-</td>
        <td>-</td>
        <td>-</td>
        <td>{{item.cfsj | isValue}}</td>
        <td>{{item.cfcs | isValue}}</td>
        <td>{{item.ddsj | isValue}}</td>
        <td>{{item.ddcs | isValue}}</td>
        <td>-</td>
        <td>-</td>
        <td>{{item.dpsj | isValue}}</td>
      </tr>
      </tbody>
    </table>
    <div class="pagination-wrap">
      <pagination :currentIndex.sync="currentIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
  </div>
</template>

<script type="text/javascript">
  import pagination from 'components/pagination';
  export default {
    name: 'booking',
    data(){
      return {
        list:[
          {yhmc:'携程',yhm:'zhangqj0130',mm:'dhjqehjq23',yhxm:'张XX',sfzh:'130291199203092310',cfsj:'2017-11-01 12:21:20',cfcs:'北京',ddsj:'2017-11-01 14:21:20',ddcs:'贵阳',dpsj:'2017-10-20 14:21:20'},
          {yhmc:'',yhm:'',mm:'',yhxm:'',sfzh:'',cfsj:'',cfcs:'',ddsj:'',ddcs:'',dpsj:''},
          {yhmc:'',yhm:'',mm:'',yhxm:'',sfzh:'',cfsj:'',cfcs:'',ddsj:'',ddcs:'',dpsj:''},
          {yhmc:'',yhm:'',mm:'',yhxm:'',sfzh:'',cfsj:'',cfcs:'',ddsj:'',ddcs:'',dpsj:''},
          {yhmc:'',yhm:'',mm:'',yhxm:'',sfzh:'',cfsj:'',cfcs:'',ddsj:'',ddcs:'',dpsj:''},
          {yhmc:'',yhm:'',mm:'',yhxm:'',sfzh:'',cfsj:'',cfcs:'',ddsj:'',ddcs:'',dpsj:''},
          {yhmc:'',yhm:'',mm:'',yhxm:'',sfzh:'',cfsj:'',cfcs:'',ddsj:'',ddcs:'',dpsj:''},
          {yhmc:'',yhm:'',mm:'',yhxm:'',sfzh:'',cfsj:'',cfcs:'',ddsj:'',ddcs:'',dpsj:''},
          {yhmc:'',yhm:'',mm:'',yhxm:'',sfzh:'',cfsj:'',cfcs:'',ddsj:'',ddcs:'',dpsj:''},
          {yhmc:'',yhm:'',mm:'',yhxm:'',sfzh:'',cfsj:'',cfcs:'',ddsj:'',ddcs:'',dpsj:''}
        ],
        // 两种方式都能返回当前页面
        currentIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 70,  // 必须传
        }
      }
    },
    methods:{
      changePage(val) {
        // 改变页码
        console.log(val)
        console.log(this.currentIndex)
      },
    },
    filters:{
      isValue(val) {
        return (val = val == "" ? "-" : val);
      }
    },
    components: {
      pagination,
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";

  .con{
    width: 100%;
    overflow-x: auto;
    padding-bottom: 50px;
  }
  .table{
    width: 1400px;
    float: left;
    thead{
      color: #8388a4;
      border-top: 1px solid #0d3196;
    }
    tr{
      border-bottom: 1px solid #0d3196;
      line-height: 38px;
      text-align: center;
    }
    tbody>tr:hover{
      background-color: #0d3196;
    }
  }
  .pagination-wrap{
    float: right;
    margin-top:15px;
  }
</style>



