<template>
  <div class="count-wrap">
    <div class="count-info">
      <span>可疑物品分类统计</span>
      <div class="date-wrap">
        <span>时间选择：</span>
        <datepicker></datepicker>
      </div>
    </div>
    <div>
      <countchart :echartsData="echartsData"></countchart>
    </div>
  </div>
</template>

<script>
  import datepicker from 'components/datepicker';
  import countchart from './countchart';
  export default {
    name: "analysecount",
    data() {
      return {
        echartsData:{
          xdata:['化学药品','金属管','刀片刀具','火药','雷管','炮竹','仿真兵器','蒙古刀','自制手枪','炸薯片'],
          data:[150,100,90,70,54,40, 34, 30,20,5]
        }
      }
    },
    components: {
      datepicker,
      countchart
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/variable";
  @import "../../assets/css/mixin";
  .count-wrap {
    .count-info {
      padding-top: 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      & > span {
        font-size: $fz-md;
        font-weight: bold;
        padding-left: 13px;
        position: relative;
        &:before {
          content: '';
          width: 3px;
          height: 16px;
          display: table-cell;
          position: absolute;
          top: 4px;
          left: 0px;
          background-color: #1083fe;
        }
      }
      .date-wrap {
        display: flex;
        justify-content: flex-end;
        align-items: center;
      }
    }
  }
</style>



