<template>
  <div>
    <table class="table">
      <thead>
      <tr>
        <td>序号</td>
        <td>体育场所</td>
        <td>体育类别</td>
        <td>体育性质</td>
        <td>同行人员</td>
        <td>活动时间</td>
        <td>体育场所预定方式</td>
        <td>预订人</td>
        <td>预订手机号</td>
        <td>预订时间</td>
        <td>备注</td>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item,index) in list">
        <td>{{index+1}}</td>
        <td>{{item.tycs| isValue}}</td>
        <td>{{item.tylb | isValue}}</td>
        <td>{{item.tyxz | isValue}}</td>
        <td>{{item.thry | isValue}}</td>
        <td>{{item.hdsj | isValue}}</td>
        <td>{{item.ydfs | isValue}}</td>
        <td>{{item.ydr | isValue}}</td>
        <td>{{item.sjh | isValue}}</td>
        <td>{{item.ydsj | isValue}}</td>
        <td>-</td>
      </tr>
      </tbody>
    </table>
    <div class="pagination-wrap">
      <pagination :currentIndex.sync="currentIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
  </div>
</template>

<script type="text/javascript">
  import pagination from 'components/pagination';
  export default {
    name: 'sports',
    data(){
      return {
        list:[
          {tycs:'五棵松体育馆',tylb:'篮球',tyxz:'体育锻炼',thry:'王XX、李XX',hdsj:'2017-12-01',ydfs:'网上预订',ydr:'张XX',sjh:'18210321892',ydsj:'2017-12-01 12:12:20'},
          {tycs:'',tylb:'',tyxz:'',thry:'',hdsj:'',ydfs:'',ydr:'',sjh:'',ydsj:''},
          {tycs:'',tylb:'',tyxz:'',thry:'',hdsj:'',ydfs:'',ydr:'',sjh:'',ydsj:''},
          {tycs:'',tylb:'',tyxz:'',thry:'',hdsj:'',ydfs:'',ydr:'',sjh:'',ydsj:''},
          {tycs:'',tylb:'',tyxz:'',thry:'',hdsj:'',ydfs:'',ydr:'',sjh:'',ydsj:''},
          {tycs:'',tylb:'',tyxz:'',thry:'',hdsj:'',ydfs:'',ydr:'',sjh:'',ydsj:''},
          {tycs:'',tylb:'',tyxz:'',thry:'',hdsj:'',ydfs:'',ydr:'',sjh:'',ydsj:''},
          {tycs:'',tylb:'',tyxz:'',thry:'',hdsj:'',ydfs:'',ydr:'',sjh:'',ydsj:''},
          {tycs:'',tylb:'',tyxz:'',thry:'',hdsj:'',ydfs:'',ydr:'',sjh:'',ydsj:''},
          {tycs:'',tylb:'',tyxz:'',thry:'',hdsj:'',ydfs:'',ydr:'',sjh:'',ydsj:''},
        ],
        // 两种方式都能返回当前页面
        currentIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 70,  // 必须传
        }
      }
    },
    methods:{
      changePage(val) {
        // 改变页码
        console.log(val)
        console.log(this.currentIndex)
      },
    },
    filters:{
      isValue(val) {
        return (val = val == "" ? "-" : val);
      }
    },
    components: {
      pagination,
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";

  .table{
    width: 100%;
    float: left;
    margin-top: 30px;
    thead{
      color: #8388a4;
      border-top: 1px solid #0d3196;
    }
    tr{
      border-bottom: 1px solid #0d3196;
      line-height: 38px;
      text-align: center;
    }
    tbody>tr:hover{
      background-color: #0d3196;
    }
  }
  .pagination-wrap{
    float: right;
    margin-top:15px;
  }
</style>



