<template>
  <div class="map-wrap">
    <div class="map-info">
      <span>可疑物品收发地图</span>
      <div class="date-wrap">
        <span>时间选择：</span>
        <datepicker></datepicker>
      </div>
    </div>
    <div class="map-cont">
      <!--<mapchart></mapchart>-->
      <img src="../../assets/img/analysemap.png" alt="流向图">
    </div>
  </div>
</template>

<script>
  import datepicker from 'components/datepicker';
  import mapchart from './mapchart';
  export default {
    name: "analysemap",
    components: {
      datepicker,
      mapchart
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/variable";
  @import "../../assets/css/mixin";
  .map-wrap {
    .map-info {
      padding: 30px 0 14px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      & > span {
        font-size: $fz-md;
        font-weight: bold;
        padding-left: 13px;
        position: relative;
        &:before {
          content: '';
          width: 3px;
          height: 16px;
          display: table-cell;
          position: absolute;
          top: 4px;
          left: 0px;
          background-color: #1083fe;
        }
      }
      .date-wrap {
        display: flex;
        justify-content: flex-end;
        align-items: center;
      }
    }
    .map-cont {
      border-radius: 5px;
      img {
        width: 100%;
      }
    }
  }
</style>



