import * as http from "./api";
const uri = 'https://mock.eolinker.com/8RiESwDd5a5506d56f9453f947d0ff45a1b5e487a9b6182?uri='
export const suspiciousPerson = {
  /**
   * 可疑人员查询--查询操作
   * @param params
   * @returns {*}
   */
  getList(params){
    return http.post({baseUri: uri, url: '/suspiciousperson/search/getList'}, params)
  },
  /**
   * 可疑人员查询--关注该人员
   * @param params
   * @returns {*}
   */
  focusPerson(params){
    return http.post({baseUri: uri, url: '/suspiciousperson/search/focusPerson'}, params)
  },
  /**
   * 可疑人员查询--加入白名单
   * @param params
   * @returns {*}
   */
  addWhiteList(params){
    return http.post({baseUri: uri, url: '/suspiciousperson/search/addWhiteList'}, params)
  }
}



