<template>
  <div class="siteAnalyze-box">
    <div class="search-box">
      <span class="item-txt">入库时间范围:</span><span class="mgl-10"><datepicker @changDate="changeDate"></datepicker></span>
      <span class="search-btn" @click="search"><i class="iconfont icon-search1187938easyiconnet"></i>查询</span>
    </div>
    <div class="siteAnalyz-map-title">可疑人员与可疑物品在地图上的分布点</div>
    <div class="echart">
      <img src="../../assets/img/可疑地点分析.png" />
    </div>
  </div>
</template>

<script type="text/javascript">
  import datepicker from 'components/datepicker';
  export default {
    name: 'siteAnalyze',
    components: {
      datepicker
    },
    data(){
      return {
      }
    },
    methods: {
      changeDate(date){
        if (date && date.start) {
          this.startDate = date.start
        }
        if (date && date.end) {
          this.endDate = date.end
        }
      },
      search() {
        console.log('search')
      }
    },
    mounted(){
      // this.getList()
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/mixin";

  .siteAnalyze-box{
    padding:20px;
  }
  .search-box {
    margin-top: 20px;
    /*font-family: MicrosoftYaHei;*/
    font-size: 14px;
    color: #c2cae2;
    position: relative;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0px;
    height: 32px;
  }

  .search-box span{
    float: left;
  }

  .item-txt {
    line-height: 40px;
  }

  .mgl-10 {
    margin-left: 10px;
  }

  .search-btn {
    display: inline-block;
    width: 94px;
    height: 30px;
    background-image: linear-gradient(-90deg, #2f5efc 0%, #0096ff 100%), linear-gradient(#03298c, #03298c);
    background-blend-mode: normal, normal;
    border-radius: 3px;
    border: solid 1px #29328f;
    text-align: center;
    line-height: 30px;
    margin-left: 30px;
    cursor: pointer;

    &:hover {
      border-color: #2c4fd2;
    }

  }

  .siteAnalyz-map-title{
    width: 100%;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
    color: #fff;
    text-indent:15px;
    margin: 25px 0 28px 0;
    border-radius: 5px;
    background-image: linear-gradient(-90deg, #09195e 0%, #082787 50%), linear-gradient(#082787, #09195e);
  }

</style>



