<template>
  <div>
    <table class="table">
      <thead>
      <tr>
        <td>序号</td>
        <td>就诊医院</td>
        <td>就诊时间</td>
        <td>就诊科室</td>
        <td>就诊医生</td>
        <td>医院地址</td>
        <td>就诊方式</td>
        <td>是否住院</td>
        <td>入住日期</td>
        <td>离职日期</td>
        <td>备注</td>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item,index) in list">
        <td>{{index+1}}</td>
        <td>{{item.jzyy | isValue}}</td>
        <td>{{item.jzsj | isValue}}</td>
        <td>{{item.jzks | isValue}}</td>
        <td>{{item.jzys | isValue}}</td>
        <td>-</td>
        <td>{{item.jzfs | isValue}}</td>
        <td>{{item.sfzy | isValue}}</td>
        <td>-</td>
        <td>-</td>
        <td>-</td>
      </tr>
      </tbody>
    </table>
    <div class="pagination-wrap">
      <pagination :currentIndex.sync="currentIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
  </div>
</template>

<script type="text/javascript">
  import pagination from 'components/pagination';
  export default {
    name: 'medical',
    data(){
      return {
        list:[
          {jzyy:'空军总医院',jzsj:'2017-12-01 09:21:32',jzks:'耳鼻喉科',jzys:'王XX',jzfs:'网上预约',sfzy:'否'},
          {jzyy:'',jzsj:'',jzks:'',jzys:'',jzfs:'',sfzy:''},
          {jzyy:'',jzsj:'',jzks:'',jzys:'',jzfs:'',sfzy:''},
          {jzyy:'',jzsj:'',jzks:'',jzys:'',jzfs:'',sfzy:''},
          {jzyy:'',jzsj:'',jzks:'',jzys:'',jzfs:'',sfzy:''},
          {jzyy:'',jzsj:'',jzks:'',jzys:'',jzfs:'',sfzy:''},
          {jzyy:'',jzsj:'',jzks:'',jzys:'',jzfs:'',sfzy:''},
          {jzyy:'',jzsj:'',jzks:'',jzys:'',jzfs:'',sfzy:''},
          {jzyy:'',jzsj:'',jzks:'',jzys:'',jzfs:'',sfzy:''},
          {jzyy:'',jzsj:'',jzks:'',jzys:'',jzfs:'',sfzy:''},
        ],
        // 两种方式都能返回当前页面
        currentIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 70,  // 必须传
        }
      }
    },
    methods:{
      changePage(val) {
        // 改变页码
        console.log(val)
        console.log(this.currentIndex)
      },
    },
    filters:{
      isValue(val) {
        return (val = val == "" ? "-" : val);
      }
    },
    components: {
      pagination,
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";

  .table{
    width: 100%;
    float: left;
    margin-top: 30px;
    thead{
      color: #8388a4;
      border-top: 1px solid #0d3196;
    }
    tr{
      border-bottom: 1px solid #0d3196;
      line-height: 38px;
      text-align: center;
    }
    tbody>tr:hover{
      background-color: #0d3196;
    }
  }
  .pagination-wrap{
    float: right;
    margin-top:15px;
  }
</style>



