<template>
  <div>
    <table class="table">
      <thead>
      <tr>
        <td>序号</td>
        <td>出发地点</td>
        <td>出发时间</td>
        <td>到达地点</td>
        <td>到达时间</td>
        <td>交通工具</td>
        <td>座位号</td>
        <td>订票方式</td>
        <td>备注</td>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item,index) in list">
        <td>{{index+1}}</td>
        <td>{{item.cfdd| isValue}}</td>
        <td>{{item.cfsj | isValue}}</td>
        <td>{{item.dd | isValue}}</td>
        <td>{{item.ddsj | isValue}}</td>
        <td>{{item.jtgj | isValue}}</td>
        <td>{{item.zwh | isValue}}</td>
        <td>{{item.dpfs | isValue}}</td>
        <td>-</td>
      </tr>
      </tbody>
    </table>
    <div class="pagination-wrap">
      <pagination :currentIndex.sync="currentIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
  </div>
</template>

<script type="text/javascript">
  import pagination from 'components/pagination';
  export default {
    name: 'travel',
    data(){
      return {
        list:[
          {cfdd:'北京',cfsj:'2017-11-01 12:31:20',dd:'贵阳',ddsj:'2017-11-01 16:20:20',jtgj:'飞机',zwh:'30F',dpfs:'网络订票'},
          {cfdd:'',cfsj:'',dd:'',ddsj:'',jtgj:'',zwh:'',dpfs:''},
          {cfdd:'',cfsj:'',dd:'',ddsj:'',jtgj:'',zwh:'',dpfs:''},
          {cfdd:'',cfsj:'',dd:'',ddsj:'',jtgj:'',zwh:'',dpfs:''},
          {cfdd:'',cfsj:'',dd:'',ddsj:'',jtgj:'',zwh:'',dpfs:''},
          {cfdd:'',cfsj:'',dd:'',ddsj:'',jtgj:'',zwh:'',dpfs:''},
          {cfdd:'',cfsj:'',dd:'',ddsj:'',jtgj:'',zwh:'',dpfs:''},
          {cfdd:'',cfsj:'',dd:'',ddsj:'',jtgj:'',zwh:'',dpfs:''},
          {cfdd:'',cfsj:'',dd:'',ddsj:'',jtgj:'',zwh:'',dpfs:''},
          {cfdd:'',cfsj:'',dd:'',ddsj:'',jtgj:'',zwh:'',dpfs:''},
        ],
        // 两种方式都能返回当前页面
        currentIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 70,  // 必须传
        }
      }
    },
    methods:{
      changePage(val) {
        // 改变页码
        console.log(val)
        console.log(this.currentIndex)
      },
    },
    filters:{
      isValue(val) {
        return (val = val == "" ? "-" : val);
      }
    },
    components: {
      pagination,
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";

  .table{
    width: 100%;
    float: left;
    margin-top: 30px;
    thead{
      color: #8388a4;
      border-top: 1px solid #0d3196;
    }
    tr{
      border-bottom: 1px solid #0d3196;
      line-height: 38px;
      text-align: center;
    }
    tbody>tr:hover{
      background-color: #0d3196;
    }
  }
  .pagination-wrap{
    float: right;
    margin-top:15px;
  }
</style>



