<template>
  <div>
    <div class="dropdown">
      <span>统计周期 :</span>
      <el-select v-model="value" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div class="box">
      <div>
        <div class="bodRi">
          <table class="table">
            <thead>
            <tr>
              <td>序号</td>
              <td>最常联系号码</td>
              <td>联系人姓名</td>
              <td>联系人地区</td>
              <td>联系次数</td>
              <td>使用号码</td>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item,index) in list">
              <td>{{index+1}}</td>
              <td>{{item.zclxhm}}</td>
              <td>{{item.xm | isValue}}</td>
              <td>{{item.dq | isValue}}</td>
              <td>{{item.cs | isValue}}</td>
              <td>{{item.hm | isValue}}</td>
            </tr>
            </tbody>
          </table>
        </div>
        <div class="pagination-wrap">
          <pagination :currentIndex.sync="currentIndex" :pageData="pageData" @changePage="changePage"></pagination>
        </div>
      </div>
      <div>
        <table class="table">
          <thead>
          <tr>
            <td>序号</td>
            <td>最常联系号码</td>
            <td>联系人姓名</td>
            <td>联系人地区</td>
            <td>联系次数</td>
            <td>使用号码</td>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item,index) in list">
            <td>{{index+1}}</td>
            <td>{{item.zclxhm}}</td>
            <td>{{item.xm | isValue}}</td>
            <td>{{item.dq | isValue}}</td>
            <td>{{item.cs | isValue}}</td>
            <td>{{item.hm | isValue}}</td>
          </tr>
          </tbody>
        </table>
        <div class="pagination-wrap">
          <pagination :currentIndex.sync="currentIndex" :pageData="pageData" @changePage="changePage"></pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  import pagination from 'components/pagination';
  export default {
    name: 'family',
    data(){
      return {
        list:[
          {zclxhm:'13609871234',xm:'张XX',dq:'山东-塘坊',cs:'31',hm:'18212345678'},
          {zclxhm:'',xm:'',dq:'',cs:'',hm:''},
          {zclxhm:'',xm:'',dq:'',cs:'',hm:''},
          {zclxhm:'',xm:'',dq:'',cs:'',hm:''},
          {zclxhm:'',xm:'',dq:'',cs:'',hm:''},
          {zclxhm:'',xm:'',dq:'',cs:'',hm:''},
          {zclxhm:'',xm:'',dq:'',cs:'',hm:''},
          {zclxhm:'',xm:'',dq:'',cs:'',hm:''},
          {zclxhm:'',xm:'',dq:'',cs:'',hm:''},
          {zclxhm:'',xm:'',dq:'',cs:'',hm:''},
        ],
        // 两种方式都能返回当前页面
        currentIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 70,  // 必须传
        },
        options:[
          {value:1,label:'最近一周'}
        ],
        value:''
      }
    },
    methods:{
      changePage(val) {
        // 改变页码
        console.log(val)
        console.log(this.currentIndex)
      },
    },
    filters:{
      isValue(val) {
        return (val = val == "" ? "-" : val);
      }
    },
    components: {
      pagination,
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";


  .bodRi{
    width: 100%;
    float: left;
    padding-right: 12px;
    border-right: 1px solid #1a4eb4;
  }
  .box>div:nth-child(1){
    width: 49%;
    float: left;
    margin-top: 30px;
  }
  .box>div:nth-child(2){
    width: 49%;
    float: right;
    margin-top: 30px;
  }
  .table{
    width: 100%;
    float: left;
    thead{
      color: #8388a4;
      border-top: 1px solid #0d3196;
    }
    tr{
      border-bottom: 1px solid #0d3196;
      line-height: 38px;
      text-align: center;
    }
    tbody>tr:hover{
      background-color: #0d3196;
    }
  }
  .pagination-wrap{
    float: right;
    margin-top:15px;
    width: 92%;
  }
</style>



