    display: inline-block;
    position: relative;
    line-height: 28px;
    .location-area-main {
      cursor: pointer;
      width: 230px;
      height: 28px;
      border: 1px solid #0143b6;
      border-radius: 2px;
      position: absolute;
      top: 8px;
      left: 0;
      padding-left: 10px;
      i {
