      border:1px solid #0090ff;
      background-color: #0072ff;
    }
    .other-person-active {
      border: 1px solid #67bb35;
      background-color: #61a845;
    }
  }
  .hidden {
    display: none;
  }
  .is-roate {
    transform: rotate(180deg);
  }
  /* 滚动条 */
  .location-area-lay::-webkit-scrollbar {
  width: 4px;
  }
  .location-area-lay::-webkit-scrollbar-track {
    background-color: transparent;
    display: none;
  }
  .location-area-lay::-webkit-scrollbar-thumb {
    background-color: #556FA6;
  }
  .location-area-lay::-webkit-scrollbar-button {
    background-color: transparent;
  }
  .location-area-lay::-webkit-scrollbar-corner {
    background-color: black;
  }

  .table /deep/ tr {
    td {
      &:nth-child(1){
        width: 70px;
      }
      &:nth-child(2){
        width: 360px;
      }
      &:nth-child(3){
        width: 235px;
      }
      &:nth-child(4){
        width: 170px;
      }
      &:nth-child(5){
        width: 135px;
      }
      &:nth-child(6){
        width: 90px;
      }
      &:nth-child(7){
        width: 175px;
      }

    }
  }
</style>



