<template>
  <div>
    <table class="table">
      <thead>
        <tr>
          <td>序号</td>
          <td>家庭关系</td>
          <td>姓名</td>
          <td>曾用名</td>
          <td>性别</td>
          <td>民族</td>
          <td>出生日期</td>
          <td>血型</td>
          <td>婚姻状况</td>
          <td>户口性质</td>
          <td>职业</td>
          <td>身法证号</td>
          <td>户籍地址</td>
        </tr>
      </thead>
      <tbody>
         <tr v-for="(item,index) in list">
           <td>{{index+1}}</td>
           <td>{{item.jtgx}}</td>
           <td>{{item.xm | isValue}}</td>
           <td>-</td>
           <td>-</td>
           <td>-</td>
           <td>{{item.csrq | isValue}}</td>
           <td>-</td>
           <td>-</td>
           <td>-</td>
           <td>-</td>
           <td>-</td>
           <td>{{item.hjdz}}</td>
         </tr>
      </tbody>
    </table>
    <div class="pagination-wrap">
      <pagination :currentIndex.sync="currentIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
  </div>
</template>

<script type="text/javascript">
  import pagination from 'components/pagination';
  export default {
    name: 'family',
    data(){
      return {
        list:[
          {jtgx:'父亲',xm:'黄家旺',csrq:'',hjdz:'河北省'},
          {jtgx:'母亲',xm:'刘玉兰',csrq:'',hjdz:'河北省'},
          {jtgx:'妻子',xm:'马桂芳',csrq:'1976-05-02',hjdz:'河北省'},
          {jtgx:'儿子',xm:'黄兴强',csrq:'',hjdz:'河北省'},
          {jtgx:'女儿',xm:'黄佩玲',csrq:'',hjdz:'河北省'},
          {jtgx:'哥哥',xm:'黄国栋',csrq:'',hjdz:'河北省'},
          {jtgx:'妹妹',xm:'',csrq:'',hjdz:'河北省'},
          {jtgx:'姐姐',xm:'',csrq:'',hjdz:'河北省'},
          {jtgx:'弟弟',xm:'',csrq:'',hjdz:'河北省'},
          {jtgx:'叔叔',xm:'',csrq:'',hjdz:'河北省'},
        ],
        // 两种方式都能返回当前页面
        currentIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 70,  // 必须传
        }
      }
    },
    methods:{
      changePage(val) {
        // 改变页码
        console.log(val)
        console.log(this.currentIndex)
      },
    },
    filters:{
      isValue(val) {
        return (val = val == "" ? "-" : val);
      }
    },
    components: {
      pagination,
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";

  .table{
    width: 100%;
    float: left;
    margin-top: 30px;
    thead{
      color: #8388a4;
      border-top: 1px solid #0d3196;
    }
    tr{
      border-bottom: 1px solid #0d3196;
      line-height: 38px;
      text-align: center;
    }
    tbody>tr:hover{
      background-color: #0d3196;
    }
  }
  .pagination-wrap{
    float: right;
    margin-top:15px;
  }
</style>



