<template>
  <div class="realistic-portrait clearfix">
    <!--realisticPortrait-->
    <div class="rea_header">
      <div class="left">
        <div class="portrait">
          <img src="../../../assets/img/portrait.png"/>
        </div>
        <ul>
          <li><span>姓名<i>:</i></span>{{xm}}</li>
          <li><span>曾用名<i>:</i></span>{{zym}}</li>
          <li><span>性别<i>:</i></span>{{xb}}</li>
          <li><span>民族<i>:</i></span>{{mz}}</li>
          <li><span>出生日期<i>:</i></span>{{csrq}}</li>
          <li><span>血型<i>:</i></span>{{xx}}</li>
          <li><span>婚姻状态<i>:</i></span>{{hyzt}}</li>
          <li><span>户口性质<i>:</i></span>{{hkxz}}</li>
          <li><span>身份证号<i>:</i></span>{{sfzh}}</li>
          <li><span>户籍地址<i>:</i></span>{{hjdz}}</li>
        </ul>
      </div>
      <div class="center">
        <div class="zhiwen">
          <div class="portrait mar10">
            <img src="../../../assets/img/fingerprint.png" class="fingerprint"/>
            <p>指纹1</p>
          </div>
          <div class="portrait mar10">
            <img src="../../../assets/img/fingerprint2.png" class="fingerprint"/>
            <p>指纹2</p>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="tubiao">
          <radar></radar>
        </div>
      </div>
    </div>

    <div class="rea_tab">
      <ul>
        <router-link tag="li" v-for="menu in menus" :to="{name:menu.name,query:query,params:params}">{{menu.title}}
        </router-link>
      </ul>
      <transition name="fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>
  </div>
</template>

<script type="text/javascript">
  import radar from '../../../components/echarts/radar'
  export default {
    name: 'realisticPortrait',
    data(){
      return {
        params: this.$route.params,
        query: this.$route.query,
        menus: [
          {title: '家庭信息', name: 'family'},
          {title: '文化教育', name: 'culture'},
          {title: '医疗信息', name: 'medical'},
          {title: '工作信息', name: 'work'},
          {title: '购物偏好', name: 'shopping'},
          {title: '通讯偏好', name: 'communication'},
          {title: '体育偏好', name: 'sports'},
          {title: '旅行信息', name: 'travel'},
          {title: '影音偏好', name: 'video'},
          {title: '游戏偏好', name: 'game'}
        ],
        xm: '黄国兴',
        zym: '黄兴',
        xb: '男',
        mz: '汉',
        csrq: '1976年5月2日',
        xx: 'O型',
        hyzt: '已婚',
        hkxz: '农业户口',
        sfzh: '130333197605021234',
        hjdz: '河北省邢台市桥东区德胜校区3号楼1单元302室'
      }
    },
    components: {
      radar,
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";
  .rea_header{
    width: 100%;
    height: 240px;
    padding-top: 40px;
    float: left;
    box-sizing: border-box;
  .left{
    width: 540px;
    float: left;
  &>ul{
      float: left;
      margin-left: 20px;
      width: 405px;
  li{
    float: left;
    margin-bottom: 4px;
  span{
    color: #c2cae2;
  }
  i{
    margin: 0 7px;
  }
  }
  li:nth-of-type(odd){
    width: 265px;
  }
  }
  }
  .center{
    width:290px;
    float: left;
  .zhiwen{
    margin-left: 45px;
  }
  }
  .right{
  .tubiao{
    float: left;
    width: 315px;
    margin-left: 10px;
    height: 175px;
    border-left: 1px solid #164bb5;
    margin-top: -15px;
  }
  }
  }
  .rea_tab{
    float: left;
    width: 100%;
  ul{
    float: left;
    width: 100%;
    height: 33px;
    background-color: #053394;
    margin: 0 auto;
    padding-left: 45px;
    box-sizing: border-box;
  li{
    float: left;
    width: 107px;
    height: 33px;
    line-height: 33px;
    text-align: center;
    color: #8794bd;
    cursor: pointer;
    /*background-color: red;*/
  }
  li.active{
    color: white;
    background-color: #2367e2;
  }
  }
  }

  .portrait{
    width: 110px;
    height: 142px;
    border: 1px solid #127df5;
    border-radius:6%;
    overflow: hidden;
    float: left;
    position: relative;
  img{
    position: relative;
    top: -1px;
    left: -1px;
    width: 117px;
    height: 147px;
  }
  img.fingerprint{
    top: -5px;
    margin-right: 10px;
  }
  p{
    width: 100%;
    height: 22px;
    line-height: 22px;
    text-align: center;
    background-color: #051b50;
    position: absolute;
    bottom: 0;
    border-top: 1px solid #127df5;
  }
  }
  .mar10{
    margin-right: 10px;
  }
</style>



