<template>
  <div class="goods-detail">
    <div class="back">
        <span @click.stop="backTo">
          <i class="iconfont icon-back"></i>
        <span>返回</span>
        </span>
    </div>
    <div class="detail-info">
      <h3 class="detail-title">
        可疑物品详情
      </h3>
      <div class="detail-list">
        <ul>
          <li><span>物品名称：</span><span>{{item.name}}</span></li>
          <li><span>快递公司：</span><span>{{item.transCorp}}</span></li>
          <li><span>疑似物品：</span><span>{{item.goodsType}}</span></li>
          <li><span>网站名称：</span><span>{{item.webSite}}</span></li>
          <li><span>发货地点：</span><span>{{item.sendFrom}}</span></li>
          <li><span>收货地点：</span><span>{{item.sendTo}}</span></li>
          <li><span>发货人：</span><span>{{item.sendPerson}}</span></li>
          <li><span>收货人：</span><span>{{item.receivePerson}}</span></li>
          <li><span>发货日期：</span><span>{{item.sendTime}}</span></li>
          <li><span>手机号：</span><span>{{item.tel}}</span></li>
          <li><span>收货日期：</span><span>{{item.reveiveTime}}</span></li>
          <!--<li><span>收货日期：</span><span>{{$store.state.goodsDetail.name}}</span></li>-->
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "goodsdetail",
    mounted() {
      // console.log(this.$router)
      console.log(this.$store.state.goodsDetail.name)
    },
    data() {
      return {
        item: {
          indexNum: 3,
          name: '组织宣传册',
          goodsType: '涉恐涉暴宣传',
          sendFrom: '深圳市',
          sendPerson: '张三',
          sendTo: '塔城',
          receivePerson: '巩宝机',
          transCorp: '韵达',
          webSite: '淘宝',
          tel: '15704361157',
          telLoc: '新疆',
          sendTime: '2017-12-01',
          reveiveTime: '2017-12-07'
        },
        backToUrl: ''
      }
    },
    methods: {
      backTo() {
        console.log(this.backToUrl)
        this.$router.push(this.backToUrl)
      }
    },
    beforeRouteEnter (to, from, next) {
      // 在渲染该组件的对应路由被 confirm 前调用
      // 不！能！获取组件实例 `this`
      // 因为当守卫执行前，组件实例还没被创建
      next((vm)=>{
        vm.backToUrl = from.path
      })
      next()
    },

  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/variable";
  /*@import "../../assets/css/mixin";*/
  .goods-detail {
    height: 100%;
    padding: 0 20px;
    /*background-color: rgba(9, 5, 45, 1);*/
    border-radius: 10px;
    .back {
      color: #c2cae2;
      padding: 28px 0;
      transition: all .2s ease-in;
      & > span {
        cursor: pointer;
        &:hover {
          color: #fff;
        }
      }
    }
    .detail-info {
      .detail-title {
        color: #fff;
        font-size: $fz-lg;
        text-align: center;
        padding-bottom: 30px;
        border-bottom: 1px #3c5989 dashed;
      }
      .detail-list {
        width: 500px;
        margin: 0 auto;
        ul {
          display: flex;
          flex-wrap: wrap;
          padding: 13px 0;
          li {
            box-sizing: border-box;
            width: 250px;
            padding: 13px 0 13px 50px;
            span:nth-child(1) {
              color: #c2cae2;
            }
          }
        }
      }
    }

  }

</style>



