								<p class="fl">
									<img :src="val.sort=='可疑人员' ? stateImg[0].img : stateImg[1].img" />
								</p>
								<div><span :class="{people:val.sort =='可疑物品'}">[{{val.sort}}]</span>
									{{val.message}}
								</div>
							</router-link>

						</ul>
					</div>
				</li>
				<li>
					<div class="home-leftb">
