						</p>
						<div class="table-box">
							<table cellspacing="0">
								<tr>
									<th>序号</th>
									<th>姓名/账号</th>
									<th>涉嫌可疑原因</th>
									<th>可疑地点</th>
								</tr>
								<tr v-for="(val,index) in personRank" :key="index">
									<td>{{index+1}}</td>
									<td>{{val.name}}</td>
									<td>{{val.reason}}</td>
									<td>{{val.area}}</td>
								</tr>
							</table>
						</div>
					</div>
				</li>
			</ul>
			<div class="home-center">
				<div @click="imgCheck">
					<p  :style="{backgroundImage:'url('+mapData[item].img+')',}" >
					  <img v-if="item == 2" src="../../assets/img/index_legend.jpg" />
					</p>
				</div>
				<div>
					<div>
						<p class="title-wrap">
							<img src="../../assets/img/index_title.png"/>
							出入界统计
						</p>
						<div class="inORout-title">重点涉恐人员：<span>{{inORoutData.emphasisPerson}}</span>人</div>
						<div class="inORout">
							<inORout></inORout>
						</div>
						<ul class="inORout-bottom">
							<li :title="'入界人员'+inORoutData.inPerson+'人'">入界人员<span>{{inORoutData.inPerson}}</span>人</li>
							<li :title="'出界人员'+inORoutData.outPerson+'人'">出界人员<span>{{inORoutData.outPerson}}</span>人</li>
						</ul>
					</div>
					<router-link tag="div" class="eventState-box" :to="{path:'/earlywarning/keyarea'}">
						<p class="title-wrap">
							<img src="../../assets/img/index_title.png"/>
							涉恐事件态势
						</p>
						<div class="eventState">
							<eventState :eventStateData="eventStateData"></eventState>
						</div>
					</router-link>
				</div>
			</div>
			<ul class="home-right">
				<li>
					<div>
						<p class="title-wrap">
							<img src="../../assets/img/index_title.png"/>
							涉恐原因分布
						</p>
						<div class="ageORsex">
							<reasonDistribute :reasonDistributeData="reasonDistributeData"></reasonDistribute>
						</div>
					</div>
				</li>
				<li>
					<div>
						<p class="title-wrap">
							<img src="../../assets/img/index_title.png"/>
							涉恐来源分布
						</p>
						<div class="ageORsex">
							<sourceDistribute :sourceDistributeData="sourceDistributeData"></sourceDistribute>
						</div>
					</div>
				</li>
				<li>
					<div>
						<p class="title-wrap">
							<img src="../../assets/img/index_title.png"/>
							涉恐人员年龄性别分布
						</p>
						<div class="ageORsex">
							<ageORsex :ageORsexData="ageORsexData"></ageORsex>
