						reason: "备注涉恐组织名称，维族邮件含涉恐内容",
						area: "振华路"
					},
				],
				//实时动态
				dynamicState: [{
						message: "石家庄裕华路清河路交叉口南200米发现涉恐可疑人员",
						sort: "可疑人员",
						id:'1',
					},
					{
						message: "石家庄裕华路清河路交叉口南200米发现涉恐可疑人员",
						sort: "可疑物品",
						id:'1',
					},
					{
						message: "石家庄裕华路清河路交叉口南200米发现涉恐可疑人员",
						sort: "可疑人员",
						id:'1',
					},
					{
						message: "石家庄裕华路清河路交叉口南200米发现涉恐可疑人员",
						sort: "可疑人员",
						id:'1',
					},
				],
//				
				//涉恐原因分布
				reasonDistributeData: [{
					value: 50,
					name: '反社会反人类'
				}, {
					value: 20,
					name: '暴力倾向'
				}, {
					value: 11,
					name: '宗教信仰'
				}, {
					value: 19,
					name: '民族矛盾'
				}],
				//涉恐事件态势
				eventStateData: {
					time: ["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月", "10月", "11月", "12月"],
					suspiciousperson: ["12", "10", "9", "6", "22", "42", "12", "22", "2", "52", "12", "32"],
					suspiciousgoods: ["22", "30", "29", "16", "12", "12", "32", "42", "12", "32", "42", "22"],
				},
				//涉恐人员年龄性别分布
				ageORsexData: {
					age: ["<20", "20-30", "30-40", "40-50", "60-70"],
					man: ["12", "10", "9", "6", "22", "42"],
					woman: ["22", "30", "29", "16", "12", "12"],
				},
				//涉恐来源分布
				sourceDistributeData: {
					area: ["宁夏", "青海", "甘肃", "内蒙古", "新疆"],
					distribute: ['45', '40', '36', '25', '18']
				},
				//出入界统计
				inORoutData: {
					inPerson: 212,
					outPerson: 88,
					emphasisPerson: 300,
				},
				item: 0,
				src: '',
			}
		},
		mounted() {
		},
		methods: {
			imgCheck() {
				this.item++
				if(this.item == 3) {
					this.item = 0
				}
			},
			slideTo(i) {
         this.$router.push(`/suspiciousperson/personCheck/trackDetail/${i}/keyArea`)
			}
		},
		components: {
			footerwrap,
			ageORsex,
			eventState,
			inORout,
			reasonDistribute,
			sourceDistribute,

			swiper,
			swiperSlide
