<style lang="scss" scoped>
	@import "~assets/css/variable";
	@import "~assets/css/mixin";
	.home-wrap {
		width: 100%;
		height: 850px;
		min-width: 1200px;
		box-sizing: border-box;
		padding: 30px;
		display: flex;
		color: #fff;
		.home-left {
			width: 21.6%;
			height: 100%;
			display: flex;
			flex-direction: colmoum;
			flex-wrap: wrap;
			align-content: space-between;
			&>li {
				position: relative;
				z-index: 3;
				width: 100%;
				height: 32%;
				box-sizing: border-box;
				border-radius: 10px;
				background: rgba(10, 35, 107, 0.7);
			}
		}
		.home-right {
			width: 21.6%;
			height: 100%;
			display: flex;
			flex-direction: colmoum;
			flex-wrap: wrap;
			align-content: space-between;
			&>li {
				position: relative;
				z-index: 3;
				width: 100%;
				height: 32%;
				box-sizing: border-box;
				border-radius: 10px;
				background: rgba(10, 35, 107, 0.7);
				.title-wrap {
					height: 46px;
				}
				.ageORsex {
					height: 200px;
				}
			}
		}
		.home-center {
			width: 56.7%;
			height: 100%;
			display: flex;
			flex-wrap: wrap;
			&> :first-child {
				position: relative;
				z-index: 3;
				cursor: pointer;
				width: 100%;
				height: 68%;
				padding: 0 20px 20px;
				box-sizing: border-box;
				& > p{
					position: relative;
					height: 517px;
					background-image: url("../../assets/img/中国地图.png");
					background-position: center top;
					background-size: 1044px;
					img{
						position:absolute;
						left:20px;
						bottom:20px;
					}
				}
			}
			&> :last-child {
				position: relative;
				z-index: 3;
				width: 100%;
				height: 32%;
				box-sizing: border-box;
				padding: 0 20px;
				display: flex;
				justify-content: space-between;
				&>:first-child {
					width: 31.2%;
					height: 100%;
					background: rgba(10, 35, 107, 0.7);
					border-radius: 10px;
					.inORout-title {
						cursor: pointer;
						padding: 0 20px;
						line-height: 20px;
						span {
							font-size: 24px;
							color: #ffd200;
							text-decoration: underline;
						}
					}
					.inORout-bottom {
						padding: 0 20px;
						display: flex;
						li {
							width: 50%;
							font-size: 12px;
							overflow: hidden;
							overflow: hidden;
							text-overflow:ellipsis;
							white-space: nowrap;
							span {
								color: #ff6600;
								text-decoration: underline;
							}
						}
					}
					.inORout {
						height: 130px;
					}
				}
				.eventState-box {
					cursor: pointer;
					width: 66.7%;
					height: 100%;
					background: rgba(9, 31, 85, 0.7);
					border-radius: 10px;
					.title-wrap {
						height: 46px;
					}
					.eventState {
						height: 200px;
					}
				}
			}
		}
		.title-wrap {
			font-size: 16px;
			height: 56px;
			line-height: 56px;
			box-sizing: border-box;
			img{
				margin-left: -8px;
				margin-right: -7px;
			}
		}
		.home-leftt {
			& >div{
				padding:0 53px;
				position: relative;
				
			}
			.swiper-button-prev{
				height: 102px;
				top:27px;
				width: 24px;
				left:30px;
				background:url("../../assets/img/index_bgl.png") no-repeat 100% 100%;
			}
			.swiper-button-next{
				height: 102px;
				width: 24px;
				top:27px;
				right:30px;
				background:url("../../assets/img/index_bgr.png") no-repeat 100% 100%;
			}
			.swiper {
				/*width: 292px;*/
				height: 142px;
				text-align: center;
				.swiper-slide {
					p{
						font-size: 12px;
						margin-top: 8px;
					}
					img{
						width: 100%;
					}
					p{
						vertical-align: middle;
					}
				}
			}
		}
		.home-leftc {
			&>ul {
				padding-left: 20px;
				padding-right: 4px;
				font-size: 12px;
				color: #fff;
				height: 173px;
				overflow: auto;
				li {
					height: 50px;
					border-bottom: 1px dashed #145B9B;
					p {
						width: 44px;
						height: 50px;
						line-height: 20px;
						text-align: center;
						box-sizing: border-box;
						padding-top: 10px;
					}
					div {
						padding-top: 6px;
						box-sizing: border-box;
						span{
							color:#1ef2ff;
						}
						.people{
							color:#ffe50e;
						}
					}
					a {
						color: #fff;
					}
				}
				&>li:first-child {
					border-top: 1px dashed #145B9B;
				}
				li:hover {
					text-decoration: underline;
					background: #135CB7;
				}
			}
		}
		.home-leftb {
			&>div {
				padding-left: 20px;
				padding-right: 4px;
				height: 173px;
				overflow: auto;
				&>table {
					font-size: 12px;
					width: 100%;
					box-sizing: border-box;
					border-top: 1px solid #0569ad;
					text-align: center;
					tr {
						border-bottom: 1px solid #0569ad;
						th {
							height: 30px;
							text-align: center;
							color: #999;
						}
						td {
							height: 42px;
						}
						&>td:nth-child(3) {
							width: 140px;
							text-align: left;
						}
					}
				}
			}
		}
		.state-box,
		.table-box {
			margin-right: 12px !important;
		}
		scrollbar-face-color: #556FA6;
	}
	/*.foot-wrap {
		position: relative;
		left: 0;
		top: 64px;
	}*/
	::-webkit-scrollbar {
		width: 4px;
	}
	::-webkit-scrollbar-track {
		background-color: transparent;
		display: none;
	}
	::-webkit-scrollbar-thumb {
		background-color: #556FA6;
	}
	::-webkit-scrollbar-button {
		background-color: transparent;
		display: none;
	}
	::-webkit-scrollbar-corner {
		background-color: black;
	}

	
</style>


