import {post} from "./api";
const uri = 'https://result.eolinker.com/8RiESwDd5a5506d56f9453f947d0ff45a1b5e487a9b6182?uri='
export const setting = {
  /**
   * 设置--涉恐关键词--获取列表
   * @param params
   * @returns {*}
   */
  getList(params){
    return post(
      {baseUri: uri, url: '/setting/keywords/getList'},
      params
    )
  },
   /**
   * 设置--数据采集规则--获取列表
   * @param params
   * @returns {*}
   */
  getList1(params){
    return post(
      {baseUri: uri, url: '/setting/rule/getList'},
      params
    )
  },
  /* 设置--重点人员--获取列表
  * @param params
  * @returns {*}
  */
  getList2(params){
    return post(
      {baseUri: uri, url: '/setting/keyperson/getList'},
      params
    )
  }
}



