import Vue from "vue";
import Router from "vue-router";
import index from "pages/index/index";
import login from "pages/login/login";
import suspiciousperson from "./suspiciousperson";
import suspiciousgoods from "pages/suspiciousgoods/suspiciousgoods";
import checkgoods from "pages/suspiciousgoods/checkgoods";
import goodsdetail from 'pages/suspiciousgoods/goodsdetail';
import analysegoods from "pages/suspiciousgoods/analysegoods";
import analysemap from "pages/suspiciousgoods/analysemap";
import analysecount from "pages/suspiciousgoods/analysecount";
import suspiciouslocation from "./suspiciouslocation";
import earlywarning from "pages/earlywarning/earlywarning";
import suspiciousarticle from "pages/earlywarning/suspiciousarticle";
import keyarea from "pages/earlywarning/keyarea";
import setting from "pages/setting/setting";
import portrait from "./portrait";
import keywords from "pages/setting/keywords";
import rule from "pages/setting/rule";
import people from "pages/setting/people";
import areas from "pages/setting/areas";

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [{
    path: '/', //路由默认跳转
    redirect: 'login'
  },
    {
      path: '/index',
      name: 'index',
      component: index,
      meta: {
        title: '首页-猎狐大数据'
      }
    },
    {
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        title: '登录-猎狐大数据'
      }
    },
    ...suspiciousperson,
    {
      path: '/suspiciousgoods',
      name: 'suspiciousgoods',
      component: suspiciousgoods,
      meta: {
        title: '可疑物品分析-猎狐大数据'
      },
      redirect: '/suspiciousgoods/checkgoods',
      children: [{
        path: 'checkgoods',
        name: 'checkgoods',
        component: checkgoods,
        meta: {
          title: '可疑物品查询-可疑物品分析-猎狐大数据'
        },

      },{
        path: 'checkgoods/detail/:id',
        name: 'detail',
        component: goodsdetail,
        meta: {
          title: '可疑物品详情-可疑物品分析-猎狐大数据'
        }
      },
        {
        path: 'analysegoods',
        name: 'analysegoods',
        component: analysegoods,
        meta: {
          title: '可疑物品分析-可疑物品分析-猎狐大数据'
        },
        redirect: '/suspiciousgoods/analysegoods/analysemap',
        children: [{
          path: 'analysemap',
          name: 'analysemap',
          component: analysemap,
          meta: {
            title: '可疑物品收发地图-可疑物品分析-可疑物品分析-猎狐大数据'
          }
        }, {
          path: 'analysecount',
          name: 'analysecount',
          component: analysecount,
          meta: {
            title: '可疑物品分类统计-可疑物品分析-可疑物品分析-猎狐大数据'
          }
        },]
      },]
    },
    ...suspiciouslocation,
    {
      path: '/earlywarning',
      name: 'earlywarning',
      component: earlywarning,
      meta: {
        title: '预警-猎狐大数据'
      },
      redirect: '/earlywarning/suspiciousarticle',
      children: [{
        path: 'suspiciousarticle',
        name: 'suspiciousarticle',
        component: suspiciousarticle,
        meta: {
          title: '预警-可疑物品收发预警-猎狐大数据'
        }
      }, {
        path: 'keyarea',
        name: 'keyarea',
        component: keyarea,
        meta: {
          title: '预警-重点区域人员预警-猎狐大数据'
        }
      },]
    },
    {
      path: '/setting',
      name: 'setting',
      component: setting,
      redirect: '/setting/keywords',
      children: [
        /*{
         path: '',
         name: 'keywords',
         component: keywords,
         meta: {
         title: '涉恐关键词-设置-猎狐大数据'
         }
         },*/
        {
          path: 'keywords',
          name: 'keywords',
          component: keywords,
          meta: {
            title: '涉恐关键词-设置-猎狐大数据'
          }
        },
        {
          path: 'rule',
          name: 'rule',
          component: rule,
          meta: {
            title: '数据采集规则-设置-猎狐大数据'
          }
        },
        {
          path: 'people',
          name: 'people',
          component: people,
          meta: {
            title: '重点人员-设置-猎狐大数据'
          }
        },
        {
          path: 'area',
          name: 'area',
          component: areas,
          meta: {
            title: '重点区域-设置-猎狐大数据'
          }
        }
      ],
      meta: {
        title: '设置-猎狐大数据'
      }
    },
    // {
    //   path: '/setting/portrait',
    //   name: 'setting2',
    //   component: setting2,
    // }
    ...portrait
  ],
  linkActiveClass: 'active'
})
router.beforeEach((to, from, next) => {
  if (to.path === '/login' || localStorage.getItem('accessToken')) {
    next()
  } else if (!localStorage.getItem('accessToken')) {
    next({path: '/'})
  }
})
// 导航确认以后才去改变title
router.afterEach((to) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
})
export default router



