<template>
    <div class="header-wrap">
      <div class="header-info" :class="{'width-header-info': isWidth}">
        <router-link tag="li" :to="{name: 'index'}">
          <h1 class="header-title" title="首页">猎狐大数据</h1>
        </router-link>
        <div class="user-info">
          <div>
            <i class="iconfont icon-user user-icon"></i>
            <span class="user">王警官</span>
            <i class="iconfont arrow icon-zelvxuanzefeiyongdaosanjiaoxingfandui"></i>
          </div>
          <router-link tag="i" :to="{name: 'login'}">
            <i class="iconfont icon-logout logout" @click="logout" title="登出"></i>
          </router-link>
        </div>
      </div>
      <div class="nav">
        <ul class="nav-list" :class="{'width-nav-list': isWidth}">
          <!-- tag="li" 可以设定 router-link生成什么样的页面元素 -->
          <router-link class="item" tag="li" :to="{name:'index'}">
            <i class="iconfont icon-home"></i>
            <span>首页</span>
          </router-link>
          <router-link class="item" tag="li" :to="{name:'suspiciousperson'}">
            <i class="iconfont icon-people"></i>
            <span>可疑人员分析</span>
          </router-link>
          <router-link class="item" tag="li" :to="{name:'suspiciousgoods'}">
            <i class="iconfont icon-briefcase"></i>
            <span>可疑物品分析</span>
          </router-link>
          <router-link class="item" tag="li" :to="{name:'suspiciouslocation'}">
            <i class="iconfont icon-Location"></i>
            <span>可疑地点分析</span>
          </router-link>
          <router-link class="item" tag="li" :to="{name:'earlywarning'}">
            <i class="iconfont icon-yujing"></i>
            <span>预警</span>
          </router-link>
          <router-link class="item setting" tag="li" :to="{name: 'setting'}" @mouseover.native='showList' @mouseout.native="hideList">
            <i class="iconfont icon-setting"></i>
            <span>设置</span>
            <div class="set-list" :class="{'show-set-list': isShowList}">
              <ul @click = 'hideList'>
                <router-link tag="li" to="/setting/keywords">涉恐关键词</router-link>
                <router-link tag="li" to="/setting/rule">数据采集规则</router-link>
                <router-link tag="li" to="/setting/people">重点人员</router-link>
                <router-link tag="li" to="/setting/area">重点区域</router-link>
              </ul>
            </div>
          </router-link>
        </ul>
      </div>
    </div>
</template>

<script type="text/javascript">

  export default {
    name: 'headers',
    data() {
      return {
        isShowList: false
      }
    },
    props: {
      isWidth: {
        type: Boolean,
        default: false
      }
    },
    watch: {

    },
    methods: {
      logout() {
      //  清楚cookie session
        localStorage.removeItem('accessToken');
      },
      showList() {
        this.isShowList = true
        console.log('click')
      },
      hideList() {
        this.isShowList = false
      }
    },

  }
</script>

<style lang="scss" scoped>
  @import "../assets/css/variable";
  @import "../assets/css/mixin";
  .header-wrap{
    width: 100%;
    min-width: $site-width;
    height: 90px;
    margin: 0 auto;
    box-sizing: border-box;
    /*background-color: #0643dc;*/
    background: linear-gradient(to right, #03257e 5%, #0445df 50%, #03257e);
    .header-info {
      color: #fff;
      width: $site-width;
      height: 58px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
      box-sizing: border-box;
      li:nth-child(1) {
        position: relative;
        &:before {
          content: '';
          display: table-cell;
          width: 940px;
          height: 58px;
          position: absolute;
          top: 0;
          left: -350px;
          background: url("../assets/img/icon_bg.png") center no-repeat;
          z-index: 0;
        }
      }
      .header-title {
        font-size: 26px;
        font-weight: bolder;
        letter-spacing: 3px;
        text-shadow:3px 1px 1px #35afe5;
        line-height: 58px;
        cursor: pointer;
        position: relative;
        z-index: 111;
      }
      .user-info > div:hover {
        & > .iconfont {
          color: #fff !important;
        }

      }
      .logout {
        margin-left: 20px;
        color: #5397ec;
        &:hover {
          color: #fff;
        }
      }
      i, span {
        cursor: pointer;
      }
      .user-info > div {
        display: inline-block;
        .iconfont {
          color: #5397ec;
          &:hover {
            color: #fff;
          }
        }
        .arrow {
          line-height: 14px;
          font-size: $fz-st !important;
        }
      }
    }
    .width-header-info {
      width: 100%;
      padding: 0 20px;
    }
    .nav {
      width: 100%;
      border-top: 2px #075df0 solid;
      box-sizing: border-box;
      /*background-color: #053bbf;*/
      background: linear-gradient(to right, #03184f, #053bbf, #03184f);
      & .nav-list{
        width: 1200px;
        line-height: 30px;
        display: flex;
        justify-content: flex-start;
        margin: 0 auto;
        li{
          color: $cl-txt-w;
          font-size: $fz-sm;
          padding: 0 20px;
          cursor: pointer;
          @include animation();
          &.active, &:hover{
            background-color: #075df0;
            & i {
              color: #f2dd20;
            }
          }
        }
        .setting {
          position: relative;
          .set-list {
            width: 150px;
            visibility: hidden;
            opacity: 0;
            position: absolute;
            left: 0;
            top: 30px;
            z-index: -1;
            background-color: rgba(17 ,51, 159, .8);
            @include animation();
          }
          .show-set-list {
            visibility: visible;
            z-index: 100;
            opacity: 1;
          }
        }
      }
      & .width-nav-list {
        width: 100%;
      }
    }


  }

</style>




