import {post} from "./api";
const uri = 'https://result.eolinker.com/8RiESwDd5a5506d56f9453f947d0ff45a1b5e487a9b6182?uri='
export const earlywarning = {
  /**
   * 预警--可疑物品收发预警--获取列表
   * @param params
   * @returns {*}
   */
  getList(params){
    return post(
      {baseUri: uri, url: '/earlywarning/suspiciousarticle/getList'},
      params
    )
  },
  /**
   * 预警--重点区域人员预警--获取列表
   * @param params
   * @returns {*}
   */
  getList1(params){
    return post(
      {baseUri: uri, url: '/earlywarning/keyarea/getList'},
      params
    )
  }
}



