        console.log(val)
        console.log(this.currentIndex)
      },
      changeDate(val) {
        console.log(val.start)
      },
      changeChackbox() {
        console.log(this.checkList)
      },
      claerChecked() {
        this.checkList = []
        this.$refs.picker.clearDate()
      },
      showInfo(item) {
        console.log(item)
        this.item = item
        this.isShowInfo = true
      },
      hideInfo() {
