<style lang="scss" scoped>
  @import "../../assets/css/variable";
  @import "../../assets/css/mixin";

  .goods-wrap {
    padding: 0 20px;
    position: relative;
    .date-picker-wrap {
      margin-top: 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .date-picker-left {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        span:nth-child(1) {
          color: #c2cae2;
          font-size: $fz-sm;
          font-weight: bold;
          margin-right: 20px;
        }
        .search {
          font-size: $fz-sm;
          line-height: 28px;
          text-align: center;
          background: linear-gradient(to right, #0095ff, #2e5ffc);
          border-radius: 2px;
          width: 90px;
          height: 28px;
          margin-left: 20px;
          transition: all .2s ease-in;
          &:hover {
            cursor: pointer;
            box-shadow: 0 0 2px #00bdff;
          }
        }
      }
      .date-picker-right {
        color: #a3d8ff;
        font-size: $fz-sm;
        cursor: pointer;
        transition: all .2s ease-in;
        &:hover {
          color: #fff;
        }
      }
    }
    .check-list {
      width: 100%;
      /*height: 30px;*/
      /*line-height: 30px;*/
      margin-top: 30px;
    }
    .search-info {
      width: 100%;
      height: 30px;
      line-height: 30px;
      margin-top: 30px;
      border-radius: 2px;
      padding-left: 14px;
      background: linear-gradient(to right, rgba(8, 39, 135, .9), rgba(8, 39, 135, 0));
      span {
        color: #ffd618;
      }
    }
    .table-wrap {
      width: 100%;
      padding: 30px 0;
      .table-list {
        width: 100%;
        tr, th {
          height: 40px;
          line-height: 40px;
          box-sizing: border-box;
          text-align: center;
        }
        tr {
          border-bottom: 1px #29328f solid;
          transition: all .2s ease-in;
          &:hover {
            background-color: #29328f;
          }
          &:nth-child(1) {
            border-top: 1px #29328f solid;
          }
          td:nth-child(2) {
            cursor: pointer;
          }
        }
        thead {
          color: #6b759f;
        }
      }
    }
  }
</style>



