<template>
  <div class="people">
    <ul class="btn-warp">
      <li class="add" @click="addPeople"><span class="iconfont icon-add"></span>添加</li>
      <li class="delete" @click="remove"><span class="iconfont icon-delete"></span>删除</li>
      <li class="daoru" @click="daoru"><span class="iconfont icon-daoru"></span>导入</li>
      <li class="daochu" @click="daochu"><span class="iconfont icon-daochu"></span>导出</li>
    </ul>
    <div class="table-wrap">
      <table class="table-list">
        <thead>
        <tr>
          <th><el-checkbox v-model="checkAll" v-on:change="checkAllChange()"></el-checkbox></th>
          <th>序号</th>
          <th>姓名</th>
          <th>重点人员分类</th>
          <th>身份证号</th>
          <th>手机号</th>
          <th>QQ号</th>
          <th>电子邮箱</th>
          <th>数据来源</th>
          <th>现实画像</th>
          <th>虚拟画像</th>
          <th>操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in list">
          <td><el-checkbox v-model="item.checked" v-on:change="checkChange(item)"></el-checkbox></td>
          <td>{{item.id}}</td>
          <td>{{item.name}}</td>
          <td>{{item.persontype}}</td>
          <td>{{item.IDnumber}}</td>
          <td>{{item.phone}}</td>
          <td>{{item.qq}}</td>
          <td>{{item.email}}</td>
          <td>{{item.datasource}}</td>
          <td><router-link style="color: #3efffd;text-decoration: underline;" tag="a" :to='"/"+item.realisticPortrait'>现实画像</router-link></td>
          <td><router-link style="color: #3efffd;text-decoration: underline;" tag="a" :to='"/"+item.virtualPortrait'>虚拟画像</router-link></td>
          <td><span class="iconfont icon-bj" @click="editPeople"></span><span class="iconfont icon-delete" @click="remove"></span></td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="pagination-wrap">
      <pagination :currentIndex.sync="currentIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
    <el-dialog
      title="导入"
      :visible.sync="daoruDialog"
      class="dialog-container"
      width="610px">
      <div class="dialog-box">
        <div class="form-box">
        <el-form ref="form"  label-width="140px">
            <div class="input-box fl" style="position: relative; margin:30px; margin-top: 50px">
              <el-form-item label="选择要导入的文件：">
                <el-input   placeholder="c:\涉恐关键词201716012.xlsx"></el-input>
              </el-form-item>
              <el-button type="primary" class="select-file-btn">选择文件</el-button>
            </div>
        </el-form>
        </div>
        <div class="btn-box">
          <span @click="daoruDialog = false">确定</span>
          <span @click="daoruDialog = false">取消</span>
        </div>
      </div>
    </el-dialog>
    <el-dialog
      title="导出"
      :visible.sync="daochuDialog"
      class="dialog-container"
      width="610px">
      <div class="dialog-box">
        <div class="form-box">
          <el-form ref="form"  label-width="140px">
            <div class="input-box fl" style="position: relative; margin:30px; margin-top: 50px">
              <el-form-item label="导出的文件存储于：">
                <el-input   placeholder="c:\涉恐关键词201716012.xlsx"></el-input>
              </el-form-item>
              <el-button type="primary" class="select-file-btn">导出位置</el-button>
            </div>
          </el-form>
        </div>
        <div class="btn-box">
          <span @click="daochuDialog = false">确定</span>
          <span @click="daochuDialog = false">取消</span>
        </div>
      </div>
    </el-dialog>
    <el-dialog
    title="重点人员添加"
    :visible.sync="addDialog"
    class="dialog-container"
    width="1160px">
    <div class="dialog-box">
      <div class="form-box">
        <el-form ref="form" :model="addForm" label-width="120px">
          <div class="input-box fl">
            <el-form-item label="姓名：">
              <el-input v-model="addForm.name"  placeholder="请输入姓名"></el-input>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="QQ号：">
              <el-input v-model="addForm.qqNum"  placeholder="请输入QQ号"></el-input>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="身份证号码：">
              <el-input v-model="addForm.card"  placeholder="请输入身份证号码"></el-input>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="电子邮箱：">
              <el-input v-model="addForm.email"  placeholder="请输入电子邮箱"></el-input>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="日期：">
              <el-date-picker
                v-model="addForm.date"
                type="date"
                :editable=false
                placeholder="请选择日期">
              </el-date-picker>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="重点人员分类：">
              <el-select v-model="addForm.type" placeholder="请选择重点人员分类">
                <el-option label="城市主要出入口" value="1"></el-option>
                <el-option label="人员成分复杂" value="2"></el-option>
                <el-option label="人员密集" value="3"></el-option>
              </el-select>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="手机号：">
              <el-input v-model="addForm.phone"  placeholder="请输入手机号"></el-input>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="数据来源：">
              <el-select v-model="addForm.resource" placeholder="请选择数据来源">
                <el-option label="公安重点人员库" value="1"></el-option>
                <el-option label="关注人员临时添加" value="2"></el-option>
              </el-select>
            </el-form-item>
          </div>
        </el-form>
      </div>
      <div class="btn-box">
        <span @click="addDialog = false">确定</span>
        <span @click="addDialog = false">取消</span>
      </div>
    </div>
  </el-dialog>
  <el-dialog
    title="重点人员修改"
    :visible.sync="editDialog"
    class="dialog-container"
    width="1160px">
    <div class="dialog-box">
      <div class="form-box">
        <el-form ref="form" :model="editForm" label-width="120px">
          <div class="input-box fl">
            <el-form-item label="姓名：">
              <el-input v-model="editForm.name"  placeholder="请输入姓名"></el-input>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="QQ号：">
              <el-input v-model="editForm.qqNum"  placeholder="请输入QQ号"></el-input>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="身份证号码：">
              <el-input v-model="editForm.card"  placeholder="请输入身份证号码"></el-input>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="电子邮箱：">
              <el-input v-model="editForm.email"  placeholder="请输入电子邮箱"></el-input>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="日期：">
              <el-date-picker
                v-model="editForm.date"
                type="date"
                :editable=false
                placeholder="请选择日期">
              </el-date-picker>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="重点人员分类：">
              <el-select v-model="editForm.type" placeholder="请选择重点人员分类">
                <el-option label="城市主要出入口" value="1"></el-option>
                <el-option label="人员成分复杂" value="2"></el-option>
                <el-option label="人员密集" value="3"></el-option>
              </el-select>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="手机号：">
              <el-input v-model="editForm.phone"  placeholder="请输入手机号"></el-input>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="数据来源：">
              <el-select v-model="editForm.resource" placeholder="请选择数据来源">
                <el-option label="公安重点人员库" value="1"></el-option>
                <el-option label="关注人员临时添加" value="2"></el-option>
              </el-select>
            </el-form-item>
          </div>
        </el-form>
      </div>
      <div class="btn-box">
        <span @click="editDialog = false">确定</span>
        <span @click="editDialog = false">取消</span>
      </div>
    </div>
  </el-dialog>
  <!-- 删除弹出层 -->
    <el-dialog
      title="删除"
      :visible.sync="removeDialog"
      class="dialog-container"
      width="610px">
    <div class="dialog-box isDelete">
      <img src="../../assets/img/query.png"/>
      <h4 class="character">是否确定删除所选重点人员？</h4>
      <div class="btn-box btn-main">
        <span @click="removeDialog = false">确定</span>
        <span @click="removeDialog = false">取消</span>
      </div>
    </div>
    </el-dialog>
  </div>
</template>

<script>
import pagination from "components/pagination";
import { setting as api } from "../../api/setting";
export default {
  name: "keywords",
  data() {
    return {
      daoruDialog: false,
      daochuDialog: false,
      addDialog: false,
      editDialog: false,
      removeDialog: false,
      list: [],
      // 两种方式都能返回当前页面
      currentIndex: 1,
      pageData: {
        curPage: 1,
        pageSize: 10,
        total: 7 // 必须传
      },
      //复选框集合
      addForm: {},
      editForm: {
        name:'张三',
        qqNum:'8857533',
        card:'53427654863538',
        email:'5524622@qq.com',
        date:'2017-09-11',
        type:'1',
        phone:'12345678900',
        resource:'1'

      },
      checkAll: false,
      checklist: []
    };
  },
  methods: {
    addPeople() {
      this.addDialog = true;
    },
    editPeople() {
      this.editDialog = true;
    },
    remove() {
      this.removeDialog = true;
    },
    daoru() {
      this.daoruDialog = true;
    },
    daochu() {
      this.daochuDialog = true;
    },
    changePage(val) {
      // 改变页码
    },
    checkChange(item) {
      let _this = this;
      let falg = true;
      item.checked == !item.checked;

      this.list.forEach(function(value) {
        if (!value.checked) {
          falg = false;
        }
      });
      if (falg) {
        _this.checkAll = true;
      } else {
        _this.checkAll = false;
      }
    },
    checkAllChange() {
      let _this = this;
      if (_this.checkAll) {
        this.list.forEach(function(value) {
          value.checked = true;
        });
      } else {
        this.list.forEach(function(value) {
          value.checked = false;
        });
      }
    },
    getList(params) {
      if (params && params.pageIndex) {
        this.curIndex = params.pageIndex;
      }
      let param = params || {};
      param.pageIndex = this.curIndex;
      param.pageSize = this.pageData.pageSize;
      // console.info(param);
      api.getList2(params).then(res => {
        console.log('res',res)
        if (res.code && res.code == '1') {
        console.log(res.data)
          res.data.list.forEach(value =>{
            value.checked = false;
            this.list.push(value);
          })
          this.wordNum = res.data.wordNum;
          this.pageData.curPage = this.curIndex;
          this.pageData.total = res.data.total;
        }
      });
    }
  },
  mounted() {
    this.getList();
  },
  components: {
    pagination
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/dialog.scss";
/*弹出层*/
.form-main {
  padding-top: 60px !important;
}
.form-main {
  display: inline-flex;
  p {
    width: 100px;
  }
  label {
    display: block;
    width: 328px;
    height: 28px;
    line-height: 28px;
    border: 1px solid #0243a9;
    padding-left: 10px;
    margin: 0 10px;
    border-radius: 4px;
  }
}
.btn-main {
  width: 100%;
  text-align: center;
  margin: 0px 0 40px 20px !important;
  span {
    border-radius: 3px;
    margin: 0 !important;
  }
  span:nth-child(1) {
    margin: 0 20px 0 0 !important;
  }
}
.input-type {
  width: 326px;
}
.exit {
  margin-bottom: 15px !important;
}
.isDelete {
  text-align: center;
  img {
    padding-top: 55px;
  }
}
.character {
  padding: 0px 0 40px 0 !important;
  line-height: 0px !important;
}
.dislog-main {
  padding: 0 60px !important;
}
/*弹出层*/
.people {
  padding: 30px 20px;
}
.btn-warp {
  overflow: hidden;
  margin-bottom: 20px;
}
.btn-warp li {
  float: left;
  width: 80px;
  height: 24px;
  line-height: 24px;
  margin-right: 20px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  text-align: center;
  font-size: 14px;
  cursor: pointer;
}
.btn-warp li.add {
  background: #072372;
  border: solid 1px #1850a4;
  color: #bddcff;
}
.btn-warp li.delete {
  border: solid 1px #84231c;
  background: #321e51;
  color: #ffbdbd;
}
.btn-warp li.daoru {
  border: solid 1px rgba(46, 94, 16, 1);
  background: #123659;
  color: #c1ff8f;
}
.btn-warp li.daochu {
  border: solid 1px rgba(105, 82, 12, 1);
  background: #1e3058;
  color: #ffe68f;
}
.btn-warp li span {
  margin-right: 2px;
  font-size: 14px;
}
.table-wrap {
  width: 100%;
  padding-bottom: 20px;
  .table-list {
    width: 100%;
    tr,
    th {
      height: 40px;
      line-height: 40px;
      box-sizing: border-box;
      text-align: center;
    }
    tr {
      border-bottom: 1px #29328f solid;
      transition: all 0.2s ease-in;
      &:hover {
        background-color: #29328f;
      }
      &:nth-child(1) {
        border-top: 1px #29328f solid;
      }
    }
    thead {
      color: #6b759f;
    }
    .icon-delete {
      color: #943427;
      cursor: pointer;
      font-size: 20px;
      font-weight: 700;
    }
    .icon-bj {
      color: #337823;
      cursor: pointer;
      font-size: 20px;
      margin-right: 20px;
      font-weight: 700;
    }
  }
}
</style>
<style>
.el-checkbox__inner {
  background: #0b1e6b;
  border: solid 1px rgba(44, 79, 210, 1);
}
.people .el-input__icon {
  line-height: 28px;
}
</style>



