<template>
  <div class="person-check">
    <div class="search-box">
      <span class="item-txt">入库时间范围:</span><span class="mgl-10">
      <datepicker ref="dateHolder" @changDate="changeDate" :defaultDate="defaultDate"></datepicker></span>
      <span class="search-btn" @click="search"><i class="iconfont icon-search1187938easyiconnet"></i>查询</span>
      <span class="search-clear" @click="reset">清空</span>
      <div class="suspicious-info">
        <el-checkbox-group class="search-checkbox-group" v-model="checkList" text-color="#fff">
          <el-checkbox :label="item.name" v-for="item,index in suspiciousInfos"></el-checkbox>
        </el-checkbox-group>
      </div>
      <div class="search-table-area">
        <div class="search-stats">
          全部可疑人员共<span class="gold-color">{{susPersonNum}}</span>人,本次查询结果共<span
          class="gold-color">{{pageData.total}}</span>条
        </div>
        <div class="opt-area">
          <span class="focus-opt" @click="focusPerson()">关注该人员</span>
          <span class="join-opt" @click="addWhiteList()">加入白名单</span>
        </div>
        <div>
          <table class="my-table">
            <thead>
            <tr>
              <th width="44px">
                <el-checkbox @change="selectAll" v-model="allChecked"></el-checkbox>
              </th>
              <th v-for="item in thead">{{item}}</th>
            </tr>
            </thead>
            <tbody v-for="item,index in list">
            <tr class="tbody-tr-1">
              <td colspan="10" rowspan="1"></td>
            </tr>
            <tr class="tbody-tr-2">
              <td width="44px">
                <el-checkbox @change="selectSingle" :true-label="item.id" v-model="defValue[index]"></el-checkbox>
              </td>
              <td>{{pageData.pageSize*(curIndex-1)+index+1}}</td>
              <td><router-link class="cursor-pointer" tag="span" :to='"/suspiciousperson/personCheck/trackDetail/"+item.id'>{{item.name}}</router-link></td>
              <td><router-link class="cursor-pointer" tag="span" :to='"/suspiciousperson/personCheck/trackDetail/"+item.id'>{{item.idNumber}}</router-link></td>
              <td>{{item.mobile}}</td>
              <td>{{item.qq}}</td>
              <td>{{item.email}}</td>
              <td>{{item.flag}}</td>
              <td>{{item.curFactorNum}}/{{item.totalFactorNum}}</td>
              <td>{{item.storageTime}}</td>
            </tr>
            <tr class="tbody-tr-3">
              <td colspan="10" rowspan="2">
                <div class="main-item">
                  <p><span class="txt-color">户籍所在地&nbsp;:&nbsp;</span>{{item.domicile}}</p>
                  <p><span class="txt-color">手机号归属地&nbsp;:&nbsp;</span>{{item.phoneBelong}}</p>
                </div>
                <div class="main-item">
                  <p><span class="txt-color">民族&nbsp;:&nbsp;</span>{{item.nation}}</p>
                  <p><span class="txt-color">重点区域&nbsp;:&nbsp;</span>出现{{item.keyAppearNum}}次</p>
                </div>
                <div class="main-item">
                  <p><span class="txt-color">网吧&nbsp;:&nbsp;</span>出现
                    <router-link class="cursor-pointer txt-underline" tag="span" :to='"/suspiciousperson/personCheck/trackDetail/"+item.id+"/internetCafes"'>
                      {{item.cafeAppearNum}}</router-link>次</p>
                  <p><span class="txt-color">宾馆&nbsp;:&nbsp;</span>出现
                  <router-link class="cursor-pointer txt-underline" tag="span" :to='"/suspiciousperson/personCheck/trackDetail/"+item.id+"/hotelDetail"'>
                    {{item.hotalAppearNum}}</router-link>次</p>
                </div>
                <div class="main-item">
                  <p><span class="txt-color">访问维文网站&nbsp;:&nbsp;</span>
                  <router-link class="cursor-pointer txt-underline" tag="span" :to='"/suspiciousperson/personCheck/trackDetail/"+item.id+"/visitUyghurNet"'>
                    {{item.visUyghurNum}}</router-link>次</p>
                  <p><span class="txt-color">访问涉恐网站&nbsp;:&nbsp;</span>
                  <router-link class="cursor-pointer txt-underline" tag="span" :to='"/suspiciousperson/personCheck/trackDetail/"+item.id+"/visitTerrorismNet"'>
                    {{item.visWadeNum}}</router-link>次</p>
                </div>
                <div class="main-item">
                  <p><span class="txt-color">维文收发邮件&nbsp;:&nbsp;</span>
                    <router-link class="cursor-pointer txt-underline" tag="span" :to='"/suspiciousperson/personCheck/trackDetail/"+item.id+"/emailDetail"'>
                      {{item.uyghurEmailNum}}</router-link>条
                  </p>
                  <p><span class="txt-color">使用涉恐app&nbsp;:&nbsp;</span>
                    <router-link class="cursor-pointer txt-underline" tag="span" :to='"/suspiciousperson/personCheck/trackDetail/"+item.id+"/useApp"'>
                    {{item.useWadeApp}}</router-link>次</p>
                </div>
                <div class="main-item">
                  <p class="blue-color line-height-24" @click.stop="focusPerson(item.id)">关注该人员</p>
                  <p class="green-color line-height-24" @click.stop="addWhiteList(item.id)">加入白名单</p>
                  <router-link class="orange-color line-height-24" tag="p"
                               :to='"/suspiciousperson/personCheck/trackDetail/"+item.id'>活动轨迹
                  </router-link>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
          <div class="mgt-20">
            <pagination :curIndex.sync="curIndex" :pageData="pageData" @changePage="changePage"></pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  import datepicker from 'components/datepicker';
  import pagination from 'components/pagination';
  import {suspiciousPerson as api} from '../../api/suspiciousperson'
  export default {
    name: 'personCheck',
    components: {
      pagination,
      datepicker
    },
    data(){
      return {
        suspiciousInfos: [
          {id: 'info1', name: '使用新疆籍身份证'},
          {id: 'info2', name: '使用新疆归属地的手机号'},
          {id: 'info3', name: '民族-新疆维吾尔族'},
          {id: 'info4', name: '访问维文网站'},
          {id: 'info5', name: '使用新疆证身份证在网吧上网'},
          {id: 'info6', name: '使用新疆身份证在酒店住宿'},
          {id: 'info7', name: '下载、使用涉恐APP'},
          {id: 'info8', name: '访问其他涉恐网站'},
          {id: 'info9', name: '出入重点区域'},
          {id: 'info10', name: '使用维文收发邮件'}
        ],
        curIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 4,
          total: 0,  // 必须传
        },
        susPersonNum: 0,
        checkList: [],
        thead: ['序号', '姓名', '身份证号', '手机号', 'QQ号', '电子邮箱', '关注状态', '可疑因素占比指标', '入库时间'],
        list: [],
        defaultDate:{start:'',end:''},
        startDate: '',
        endDate: '',
        params: {},
        defValue: [],
        allChecked: false,
        curNum: 0
      }
    },
    methods: {
      changeDate(date){
        if (date && date.start) {
          this.startDate = date.start
        }
        if (date && date.end) {
          this.endDate = date.end
        }
      },
      changePage(index){
        let params = this.params
        params.pageIndex = index
        this.getList(params)
      },
      reset(){
        this.curIndex = 1
        this.checkList = []
        this.startDate = ''
        this.endDate = ''
        this.$refs.dateHolder.clearDate()
        this.params = {}
        this.getList()
      },
      getParams(){
        let params = {}
        if (this.startDate !== '') {
          params.startDate = this.startDate
        }
        if (this.endDate !== '') {
          params.endDate = this.endDate
        }
        if (this.checkList.length !== 0) {
          params.conditions = this.checkList
        }
        return params
      },
      search(){
        let params = this.getParams()
        this.params = this.getParams()
        params.pageIndex = 1

        this.getList(params)
      },
      getList(params){
        this.allChecked = false
        this.curNum = 0
        this.defValue = []
        if (params && params.pageIndex) {
          this.curIndex = params.pageIndex
        }
        let param = params || {}
        param.pageIndex = this.curIndex
        param.pageSize = this.pageData.pageSize

        api.getList(param).then((rst) => {
          if(rst.code && rst.code === 1){
//          this.list = rst.data.list
          this.list = [
            {
              "id":100000,
              "name":"吐尔孙·托合提",
              "idNumber":"450702198407100312",
              "mobile":"14597344168",
              "qq":"880413",
              "email":"x.qabf@wlnkplhw.org",
              "flag":"未关注",
              "curFactorNum":23,
              "totalFactorNum":37,
              "storageTime":"2009-07-14",
              "domicile":"新疆乌鲁木齐",
              "phoneBelong":"新疆乌鲁木齐",
              "nation":"满族",
              "keyAppearNum":43,
              "cafeAppearNum":18,
              "hotalAppearNum":42,
              "visUyghurNum":38,
              "visWadeNum":56,
              "uyghurEmailNum":31,
              "useWadeApp":52
            },
            {
              "id":100001,
              "name":"沈平",
              "idNumber":"360000200209127344",
              "mobile":"18815054247",
              "qq":"501645",
              "email":"o.kyyyalyyjf@welwbmh.bg",
              "flag":" 已关注",
              "curFactorNum":22,
              "totalFactorNum":24,
              "storageTime":"2017-05-09",
              "domicile":"四川省",
              "phoneBelong":"临沂市",
              "nation":"满族",
              "keyAppearNum":25,
              "cafeAppearNum":29,
              "hotalAppearNum":38,
              "visUyghurNum":12,
              "visWadeNum":48,
              "uyghurEmailNum":31,
              "useWadeApp":49
            },
            {
              "id":100002,
              "name":"武刚",
              "idNumber":"220000201708074751",
              "mobile":"18815054247",
              "qq":"727131",
              "email":"t.guju@wiaub.ml",
              "flag":" 已关注",
              "curFactorNum":44,
              "totalFactorNum":22,
              "storageTime":"2009-09-16",
              "domicile":"贵州省",
              "phoneBelong":"舟山市",
              "nation":"壮族",
              "keyAppearNum":20,
              "cafeAppearNum":21,
              "hotalAppearNum":22,
              "visUyghurNum":46,
              "visWadeNum":34,
              "uyghurEmailNum":35,
              "useWadeApp":20
            },
            {
              "id":100003,
              "name":"陆洋",
              "idNumber":"452802195601280023",
              "mobile":"14597344168",
              "qq":"862961",
              "email":"s.fmidezud@gvjmzh.cg",
              "flag":" 已关注",
              "curFactorNum":42,
              "totalFactorNum":48,
              "storageTime":"2011-06-20",
              "domicile":"香港特别行政区",
              "phoneBelong":"佛山市",
              "nation":"满族",
              "keyAppearNum":26,
              "cafeAppearNum":15,
              "hotalAppearNum":46,
              "visUyghurNum":36,
              "visWadeNum":18,
              "uyghurEmailNum":40,
              "useWadeApp":47
            }
          ]
          this.susPersonNum = rst.data.susPersonNum
          this.pageData.curPage = this.curIndex
          this.pageData.total = rst.data.total
        }
      })
      },
      selectAll(val){
        this.defValue = []
        if (val) {
          this.curNum = this.list.length
          this.list.map((item) => {
            this.defValue.push(item.id)
        })
        } else {
          this.list.map((item) => {
            this.defValue.push('')
        })
          this.curNum = 0
        }
      },
      selectSingle(isChecked){
        if (!isChecked) {
          this.curNum--
          this.allChecked = isChecked
        } else {
          this.curNum++
          if (this.curNum === this.list.length) {
            this.allChecked = true
          }
        }
      },
      getPersonIds(id){
        let ids = []
        if (id) {
          ids.push(id)
        } else {
          this.defValue.map((item) => {
            if(item){
              ids.push(item)
            }
          }
        )
        }
        return ids
      },
      focusPerson(id){
        let ids = this.getPersonIds(id)
        let params = {ids: ids}
        if (ids.length === 0) {
          this.$message({
            message: '请选择人员!',
            type: 'warning'
          });
        } else {
          api.focusPerson(params).then((rst) => {
            if(rst.code === 1){
            this.$message({
              message: '关注该人员成功',
              type: 'success',
              onClose: this.getList(this.params)
            })
          }
        }
        )
        }
      },
      addWhiteList(id){
        let ids = this.getPersonIds(id)
        let params = {ids: ids}
        if (ids.length === 0) {
          this.$message({
            message: '请选择人员!',
            type: 'warning'
          });
        } else {
          api.addWhiteList(params).then((rst) => {
            if(rst.code === 1){
            this.$message({
              message: '加入白名单成功',
              type: 'success',
              onClose: this.getList(this.params)
            })
          }
        }
        )
        }
      }
    },
    mounted(){
      this.getList()
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";

  .search-box {
    margin-top: 20px;
    /*font-family: MicrosoftYaHei;*/
    font-size: 14px;
    color: #c2cae2;
    position: relative;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0px;
  }

  .cursor-pointer{
    cursor: pointer;
  }

  .date-picker {
    display: inline-block !important;
  }

  .item-txt {
    line-height: 40px;
  }

  .txt-color {
    color: #c2cae2;
  }

  .mgt-20 {
    margin-top: 20px;
  }

  .mgl-10 {
    margin-left: 10px;
  }

  .search-btn {
    display: inline-block;
    width: 94px;
    height: 30px;
    background-image: linear-gradient(-90deg, #2f5efc 0%, #0096ff 100%), linear-gradient(#03298c, #03298c);
    background-blend-mode: normal, normal;
    border-radius: 3px;
    border: solid 1px #29328f;
    text-align: center;
    line-height: 30px;
    margin-left: 30px;
    cursor: pointer;

  &:hover {
    border-color: #2c4fd2;
  }

  }

  .search-clear {
    position: absolute;
    display: inline-block;
    right: 10px;
    top: 0px;
    line-height: 40px;
    color: #a3d8ff;
    cursor: pointer;
  }

  .suspicious-info {
    margin-top: 20px;

  .el-checkbox {
    margin-left: 0px;
    width: 20%;
    margin-bottom: 15px;
  }

  }
  .search-stats {
    margin-top: 15px;
    height: 30px;
    background-image: linear-gradient(88deg, rgba(7, 63, 202, 0.73) 0%, rgba(7, 63, 202, 0.1) 100%);
    border-radius: 3px;
    color: #ffffff;
    line-height: 30px;
    padding-left: 12px;
    opacity: 0.8;
  }

  .gold-color {
    color: #ffd618;
  }

  .blue-color {
    color: #02a3ff;
  }

  .green-color {
    color: #45e000;
  }

  .orange-color {
    color: #ff9c00
  }

  .opt-area {
    margin-top: 28px;
    margin-bottom: 20px;
  }

  .focus-opt, .join-opt {
    width: 96px;
    height: 24px;
    border-radius: 3px;
    display: inline-block;
    text-align: center;
    line-height: 25px;
    cursor: pointer;
  }

  .focus-opt {
    color: #bddcff;
    border: solid 1px #1850a4;
    background-color: rgba(0, 102, 255, 0.17);
  }

  .join-opt {
    margin-left: 20px;
    color: #c2ffbd;
    background-color: rgba(58, 162, 22, 0.17);
    border: solid 1px #2e5e10;
  }

  .my-table {
    width: 100%;
    height: auto;
    border: none;
    font-size: 14px;
    margin: 0 auto;
    border-collapse: separate;
    border-spacing: 0;
    text-align: center;
    color: #fff;
    margin-top: 15px;

  thead {
    line-height: 41px;
    background-color: transparent;

  th {
    border-bottom: solid 1px #29328f;
    border-top: solid 1px #29328f;
    text-align: center;
    color: #6b7698;
  }

  }
  tbody {
    border: solid 1px #29328f;
  }

  tbody .tbody-tr-1 {
    height: 10px;
  }

  tbody .tbody-tr-2 {
    height: 41px;
  }

  tbody .tbody-tr-2 td {
    border-top: solid 1px #29328f;
  }

  tbody .tbody-tr-2 td:first-child, tbody .tbody-tr-3 td:first-child {
    border-left: solid 1px #29328f;
  }

  tbody .tbody-tr-2 td:last-child, tbody .tbody-tr-3 td:last-child {
    border-right: solid 1px #29328f;
  }

  tbody .tbody-tr-3 {
    height: 99px;
    background-color: rgba(0, 0, 0, 0.25);
    text-align: left;
  }

  tbody .tbody-tr-3 td {
    border-bottom: solid 1px #29328f;
  }

  tbody .tbody-tr-3 div:last-child {
    text-align: center;
  }

  .main-item {
    width: calc((100% - 160px) / 5);
    float: left;
    box-sizing: border-box;
    padding: 4px 0 0 18px;
  }

  .main-item:last-child {
    width: 160px;
  }

  .main-item p:first-child {
    line-height: 34px;
  }

  .main-item:last-child {
    padding-top: 0;
  }

  .main-item:last-child p {
    cursor: pointer;
  }

  .main-item:last-child p:first-child {
    line-height: 22px;
  }

  .line-height-24 {
    line-height: 24px;
  }

  .txt-underline {
    text-decoration: underline;
    color: #ff6600;
    display: inline-block;
    padding: 0 4px;
  }

  }

</style>



