<template>
  <div>
    <ul class="setting-tab">
      <router-link tag="li" to="/setting/keywords">涉恐关键词</router-link>
      <router-link tag="li" to="/setting/rule">数据采集规则</router-link>
      <router-link tag="li" to="/setting/people">重点人员</router-link>
      <router-link tag="li" to="/setting/area">重点区域</router-link>
    </ul>
<router-view/>
  </div>
</template>

<script type="text/javascript">
  export default {
    name: "setting"
  }
</script>

<style lang="scss" scoped>
  .setting-tab {
    color: #939fc2;
    font-size: 14px;
    display: flex;
    line-height: 26px;
    font-weight: bold;
    box-sizing: border-box;
    position: relative;
    &:after {
       content: "";
       display: block;
       width: 100%;
       height: 2px;
       position: absolute;
       background-color: #102456;
       left: 0;
       bottom: 0;
     }
    li {
      padding: 0 10px 5px;
      margin-right: 10px;
      cursor: pointer;
      position: relative;
    }
    & li:hover {
      color: #fff;
      &:after {
         content: "";
         display: block;
         width: 100%;
         height: 2px;
         position: absolute;
         background-color: #fed61e;
         left: 0;
         bottom: 0;
         z-index: 2;
      }
    }
    .active {
      box-sizing: border-box;
      font-size: 20px;
      color: #fff;
      &:after {
         content: "";
         display: block;
         width: 100%;
         height: 2px;
         position: absolute;
         background-color: #fed61e;
         left: 0;
         bottom: 0;
         z-index: 2;
      }
    }
  }

</style>



