<template>
  <div class="analyse-wrap">
    <div class="analyse-type">
      <router-link tag="div" to="/suspiciousgoods/analysegoods/analysemap">可疑物品收发地图</router-link>
      <router-link tag="div" to="/suspiciousgoods/analysegoods/analysecount">可疑物品分类统计</router-link>
    </div>
    <div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  export default {
    name: "analysegoods"
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/variable";
  @import "../../assets/css/mixin";
  .analyse-wrap {
    padding: 0 20px;
    .analyse-type {
      padding-top: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      div {
        height: 24px;
        line-height: 24px;
        box-sizing: border-box;
        padding: 0 20px;
        border-radius: 5px;
        cursor: pointer;
        &:nth-child(1) {
          border: 1px rgb(24,80,164) solid;
          background-color: rgba(24,80,164, .2);
          margin-right: 10px;
          &:hover, &.active {
            border: 1px rgb(0,144,255) solid;
            background-color: rgba(0,114,255, 1);
          }
        }
        &:nth-child(2) {
          border: 1px rgb(46,94,16) solid;
          background-color: rgba(46,94,16, .2);
          &:hover, &.active {
            border: 1px rgb(103,187,53) solid;
            background-color: rgba(97,168,69, 1);
          }
        }
      }
    }
  }
</style>



