<template>
  <div class="keywords">
    <ul class="btn-warp">
      <li class="add" @click="add"><span class="iconfont icon-add"></span>添加</li>
      <li class="delete" @click="remove"><span class="iconfont icon-delete"></span>删除</li>
      <li class="daoru" @click="daoru"><span class="iconfont icon-daoru"></span>导入</li>
      <li class="daochu" @click="daochu"><span class="iconfont icon-daochu"></span>导出</li>
    </ul>
    <div class="table-wrap">
      <table class="table-list">
        <thead>
        <tr>
          <th><el-checkbox v-model="checkAll" v-on:change="checkAllChange()"></el-checkbox></th>
          <th>序号</th>
          <th>关键词内容</th>
          <th>关键词类型</th>
          <th>涉恐类型</th>
          <th>入库时间</th>
          <th>有效使用次数</th>
          <th>数据来源</th>
          <th>系统维护人</th>
          <th>操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in list">
          <td><el-checkbox v-model="item.checked" v-on:change="checkChange(item)"></el-checkbox></td>
          <td>{{item.id}}</td>
          <td>{{item.antistop}}</td>
          <td>{{item.antistopType}}</td>
          <td>{{item.wadeType}}</td>
          <td>{{item.time}}</td>
          <td>{{item.number}}</td>
          <td>{{item.source}}</td>
          <td>{{item.maintainer}}</td>
          <td>
            <span class="iconfont icon-bj" @click="add"></span>
            <span class="iconfont icon-delete" @click="remove"></span>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="mgt-20">
      <pagination :curIndex.sync="curIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
    <!-- 添加弹出层 -->
    <el-dialog
      title="添加"
      :visible.sync="addDialog"
      class="dialog-container"
      width="610px">
    <div class="dialog-box dislog-main">
      <div class="form-box">
        <el-form ref="form" :model="addForm" label-width="120px">
         <div class="input-box fl">
            <el-form-item label="关键词内容：" class="input-exit">
              <el-input v-model="addForm.name"  placeholder="ISIS"></el-input>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="对应中文翻译：" class="input-exit">
              <el-input v-model="addForm.name"  placeholder=" 伊拉克和叙利亚伊斯兰国"></el-input>
            </el-form-item>
          </div>
          <div class="input-box fl">
            <el-form-item label="涉恐类型：" class="input-exit">
              <el-select v-model="addForm.type" placeholder="请选择涉恐类型" class="input-type">
                <el-option label="邮箱内容涉恐" value="1"></el-option>
                <el-option label="网购物品涉恐" value="2"></el-option>
              </el-select>
            </el-form-item>
          </div>
        </el-form>
      </div>
      <div class="btn-box btn-main">
        <span @click="addDialog = false">确定</span>
        <span @click="addDialog = false">取消</span>
      </div>
    </div>
    </el-dialog>
<!-- 删除弹出层 -->
    <el-dialog
      title="删除"
      :visible.sync="removeDialog"
      class="dialog-container"
      width="610px">
    <div class="dialog-box isDelete">
      <img src="../../assets/img/query.png"/>
      <h4 class="character">是否确定删除所选关键词？</h4>
      <div class="btn-box btn-main">
        <span @click="removeDialog = false">确定</span>
        <span @click="removeDialog = false">取消</span>
      </div>
    </div>
    </el-dialog>
    <el-dialog
      title="导入"
      :visible.sync="daoruDialog"
      class="dialog-container"
      width="610px">
      <div class="dialog-box">
        <div class="form-box">
          <el-form ref="form"  label-width="140px">
            <div class="input-box fl" style="position: relative; margin:30px; margin-top: 50px">
              <el-form-item label="选择要导入的文件：">
                <el-input   placeholder="c:\涉恐关键词201716012.xlsx"></el-input>
              </el-form-item>
              <el-button type="primary" class="select-file-btn">选择文件</el-button>
            </div>
          </el-form>
        </div>
        <div class="btn-box">
          <span @click="daoruDialog = false">确定</span>
          <span @click="daoruDialog = false">取消</span>
        </div>
      </div>
    </el-dialog>
    <el-dialog
      title="导出"
      :visible.sync="daochuDialog"
      class="dialog-container"
      width="610px">
      <div class="dialog-box">
        <div class="form-box">
          <el-form ref="form"  label-width="140px">
            <div class="input-box fl" style="position: relative; margin:30px; margin-top: 50px">
              <el-form-item label="导出的文件存储于：">
                <el-input   placeholder="c:\涉恐关键词201716012.xlsx"></el-input>
              </el-form-item>
              <el-button type="primary" class="select-file-btn">导出位置</el-button>
            </div>
          </el-form>
        </div>
        <div class="btn-box">
          <span @click="daochuDialog = false">确定</span>
          <span @click="daochuDialog = false">取消</span>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import pagination from "components/pagination";
import { setting as api } from "../../api/setting";
export default {
  name: "keywords",
  data() {
    return {
      daoruDialog: false,
      daochuDialog: false,
      addDialog: false,
      removeDialog: false,
      editDialog: false,
      addForm: {},
      editForm: {},
      wordNum: 120,
      curIndex: 1,
      pageData: {
        curPage: 1,
        pageSize: 10,
        total: 0 // 必须传
      },
      list: [],
      checkAll: false
    };
  },
  methods: {
    daoru() {
      this.daoruDialog = true;
    },
    daochu() {
      this.daochuDialog = true;
    },
    add() {
      this.addDialog = true;
    },
    remove() {
      this.removeDialog = true;
    },
    changePage(index) {
      this.list = [];
      this.getList({ pageIndex: index });
    },
    checkChange(item) {
      let _this = this;
      let falg = true;
      item.checked == !item.checked;

      this.list.forEach(function(value) {
        if (!value.checked) {
          falg = false;
        }
      });
      if (falg) {
        _this.checkAll = true;
      } else {
        _this.checkAll = false;
      }
    },
    checkAllChange() {
      let _this = this;
      if (_this.checkAll) {
        this.list.forEach(function(value) {
          value.checked = true;
        });
      } else {
        this.list.forEach(function(value) {
          value.checked = false;
        });
      }
    },
    getList(params) {
      if (params && params.pageIndex) {
        this.curIndex = params.pageIndex;
      }
      let param = params || {};
      param.pageIndex = this.curIndex;
      param.pageSize = this.pageData.pageSize;
      // console.info(param);
      api.getList(params).then(res => {
        if (res.code && res.code == '1') {
          res.data.list.forEach(value =>{
            value.checked = false;
            this.list.push(value);
          })
          this.wordNum = res.data.wordNum;
          this.pageData.curPage = res.data.curIndex;
          this.pageData.total = res.data.total;
        }
      });
    }
  },
  mounted() {
    this.getList();
  },
  components: {
    pagination
  }
};
</script>

<style lang="scss" scoped>
/*弹出层*/
.form-main {
  padding-top: 60px !important;
}
.form-main {
  display: inline-flex;
  p {
    width: 100px;
  }
  label {
    display: block;
    width: 328px;
    height: 28px;
    line-height: 28px;
    border: 1px solid #0243a9;
    padding-left: 10px;
    margin: 0 10px;
    border-radius: 4px;
  }
}
.btn-main {
  width: 100%;
  text-align: center;
  margin: 0px 0 40px 20px !important;
  span {
    border-radius: 3px;
    margin: 0 !important;
  }
  span:nth-child(1) {
    margin: 0 20px 0 0 !important;
  }
}
.input-type {
  width: 326px;
}
.exit {
  margin-bottom: 15px !important;
}
.isDelete {
  text-align: center;
  img {
    padding-top: 55px;
  }
}
.character {
  padding: 0px 0 40px 0 !important;
  line-height: 0px !important;
}
.dislog-main {
  padding: 0 60px !important;
}
/*弹出层*/
.keywords {
  padding: 30px 20px;
}
.btn-warp {
  overflow: hidden;
  margin-bottom: 20px;
}
.btn-warp li {
  float: left;
  width: 80px;
  height: 24px;
  line-height: 24px;
  margin-right: 20px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  text-align: center;
  font-size: 14px;
  cursor: pointer;
}
.btn-warp li.add {
  background: #072372;
  border: solid 1px #1850a4;
  color: #bddcff;
}
.btn-warp li.delete {
  border: solid 1px #84231c;
  background: #321e51;
  color: #ffbdbd;
}
.btn-warp li.daoru {
  border: solid 1px rgba(46, 94, 16, 1);
  background: #123659;
  color: #c1ff8f;
}
.btn-warp li.daochu {
  border: solid 1px rgba(105, 82, 12, 1);
  background: #1e3058;
  color: #ffe68f;
}
.btn-warp li span {
  margin-right: 2px;
  font-size: 14px;
}
.table-wrap {
  width: 100%;
  padding-bottom: 20px;
  .table-list {
    width: 100%;
    tr,
    th {
      height: 40px;
      line-height: 40px;
      box-sizing: border-box;
      text-align: center;
    }
    tr {
      border-bottom: 1px #29328f solid;
      transition: all 0.2s ease-in;
      &:hover {
        background-color: #29328f;
      }
      &:nth-child(1) {
        border-top: 1px #29328f solid;
      }
    }
    thead {
      color: #6b759f;
    }
    .icon-delete {
      color: #943427;
      cursor: pointer;
      font-size: 20px;
      font-weight: 700;
    }
    .icon-bj {
      color: #337823;
      cursor: pointer;
      font-size: 20px;
      margin-right: 20px;
      font-weight: 700;
    }
  }
}
</style>

<style>
.el-checkbox__inner {
  background: #0b1e6b;
  border: solid 1px rgba(44, 79, 210, 1);
}
</style>



