<template>
  <div class="suspicious-person">
    <ul class="second-nav">
      <router-link v-for="menu in menus" tag="li" :to="menu.path">{{menu.title}}</router-link>
    </ul>
    <transition name="fade" mode="out-in">
      <router-view class="pd-20"></router-view>
    </transition>
  </div>
</template>

<script type="text/javascript">

  export default {
    name: 'suspiciousperson',
    data(){
      return {
        menus: [
          {title: '可疑人员查询', path: '/suspiciousperson/personCheck'},
          {title: '可疑人员统计', path: '/suspiciousperson/personStats'}
        ]
      }
    },
    computed:{
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/variable";
  @import "../../assets/css/mixin";

  .pd-20{
    padding: 20px;
  }
  .second-nav {
    color: #49637c;
    font-size: $fz-sm;
    display: flex;
    line-height: 26px;
    font-weight: bold;
    box-sizing: border-box;
    /*border-bottom: 2px #102456 solid;*/
    position: relative;

  &:after {
    content: '';
    display: block;
    width: 100%;
    height: 2px;
    position: absolute;
    background-color: #102456;
    left: 0;
    bottom: 0;
  }

  li {
    padding: 0 10px 5px;
    margin-right: 10px;
    cursor: pointer;
    position: relative;
    color:#939fc2;
  }

  & li:hover {
    color: #fff;

  /*border-bottom: 2px #fed61e solid;*/
  &:after {
    content: '';
    display: block;
    width: 100%;
    height: 2px;
    position: absolute;
    background-color: #ffd618;
    left: 0;
    bottom: 0;
    z-index: 2;
  }

  }
  .active {
    box-sizing: border-box;
    /*border-bottom: 2px #fed61e solid;*/
    font-size: 20px;
    color: #fff;

  &:after {
    content: '';
    display: block;
    width: 100%;
    height: 2px;
    position: absolute;
    background-color: #ffd618;
    left: 0;
    bottom: 0;
    z-index: 2;
  }
  }
  }
</style>



