<template>
  <div class="person-stat">
    <div class="mgt-20 clearfix">
      <span class="border-item">可疑人员涉恐特征统计</span>
      <span class="fr"><span class="color-span">时间选择&nbsp;:&nbsp;</span><datepicker
        @changDate="changeDate"></datepicker></span>
    </div>

    <div class="clearfix">
      <div class="chart-box">
        <countchartsingle :echartsData="echartsDataSingle"></countchartsingle>
      </div>

      <div class="mgt-40 clearfix">
        <span class="border-item">可疑人员涉恐项数量统计</span>
        <span class="fr"><span class="color-span">时间选择&nbsp;:&nbsp;</span><datepicker
          @changDate="changeDateOther"></datepicker></span>
      </div>

      <div class="chart-box">
        <countchartitems :echartsData="echartsDataItems"></countchartitems>
      </div>

    </div>
  </div>
</template>

<script type="text/javascript">
  import datepicker from 'components/datepicker';
  import countchartsingle from './charts/countchartsingle.vue';
  import countchartitems from './charts/countchartitems';

  export default {
    components: {
      datepicker,
      countchartsingle,
      countchartitems
    },
    name: 'personStats',
    data() {
      return {
        echartsDataSingle: {
          xdata: ['持有新疆籍身份证', '使用新疆身份证在网吧上网', '使用新疆归属地的手机号', '民族-新疆维吾尔族', '访问维文网站', '访问其他涉恐网站', '出入重点区域', '下载、使用涉恐APP', '使用新疆身份在酒店住宿', '使用维文收发邮件'],
          data: [155, 20, 25, 110, 40, 10, 15, 30, 9, 35]
        },
        echartsDataItems: {
          xdata: ['符合全部条件', '符合9项条件', '符合8项条件', '符合7项条件', '符合6项条件', '符合5项条件', '符合4项条件', '符合3项条件', '符合2项条件', '符合1项条件'],
          data: [150, 100, 90, 70, 54, 40, 34, 30, 20, 5]
        }
      }
    },
    methods: {
      changeDate(date) {
        this.echartsDataSingle = {
          xdata: ['持有新疆籍身份证', '使用新疆身份证在网吧上网', '使用新疆归属地的手机号', '民族-新疆维吾尔族', '访问维文网站', '访问其他涉恐网站', '出入重点区域', '下载、使用涉恐APP', '使用新疆身份在酒店住宿', '使用维文收发邮件'],
          data: [15, 120, 25, 110, 40, 10, 115, 30, 9, 35]
        }
      },
      changeDateOther(date) {
        this.echartsDataItems = {
          xdata: ['符合全部条件', '符合9项条件', '符合8项条件', '符合7项条件', '符合6项条件', '符合5项条件', '符合4项条件', '符合3项条件', '符合2项条件', '符合1项条件'],
          data: [50, 110, 190, 70, 154, 50, 34, 6, 20, 15]
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .border-item {
    display: inline-block;
    /*font-family: MicrosoftYaHei;*/
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 17px;
    letter-spacing: 0px;
    color: #ffffff;
    border-left: solid 2px #1083fe;
    padding-left: 10px;
  }

  .mgt-20 {
    margin-top: 20px;
  }

  .mgt-40 {
    margin-top: 40px;
  }

  .date-picker {
    display: inline-block !important;
  }

  .color-span {
    color: #c2cae2;
  }

  .chart-box {
    width: 100%;
    height: 360px;
    margin: 20px 0;
  }
</style>



