<template>
  <div class="goods-wrap">
    <ul class="goods-nav">
      <router-link tag="li" to="/suspiciousgoods/checkgoods">可疑物品查询</router-link>
      <router-link tag="li" to="/suspiciousgoods/analysegoods">可疑物品分析</router-link>
    </ul>
    <transition name="fade" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script type="text/javascript">

  export default {
    name: 'suspiciousgoods',
    data() {
      return {

      };
    },
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/variable";
  @import "../../assets/css/mixin";

  .goods-wrap {
    .fade-enter-active, .fade-leave-active {
      transition: all .1s linear;
    }
    .fade-enter, .fade-leave-to {
      opacity: 0;
      /*height: 0;*/
    }
    .goods-nav {
      color: #c2cae2;
      font-size: $fz-sm;
      display: flex;
      line-height: 26px;
      font-weight: bold;
      box-sizing: border-box;
      /*border-bottom: 2px #102456 solid;*/
      position: relative;
      &:after {
        content: '';
        display: block;
        width: 100%;
        height: 2px;
        position: absolute;
        background-color: #102456;
        left: 0;
        bottom: 0;
      }
      li {
        padding: 0 10px 5px;
        margin-right: 10px;
        cursor: pointer;
        position: relative;
      }
      & li:hover{
        color: #fff;
        /*border-bottom: 2px #fed61e solid;*/
        &:after {
          content: '';
          display: block;
          width: 100%;
          height: 2px;
          position: absolute;
          background-color: #fed61e;
          left: 0;
          bottom: 0;
          z-index: 2;
        }
      }
      .active {
        box-sizing: border-box;
        /*border-bottom: 2px #fed61e solid;*/
        font-size: 20px;
        color: #fff;
        &:after {
          content: '';
          display: block;
          width: 100%;
          height: 2px;
          position: absolute;
          background-color: #fed61e;
          left: 0;
          bottom: 0;
          z-index: 2;
        }
      }
    }
  }
</style>



