<template>
	<div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

	</div>
</template>

<script>
	import {chartMixin} from 'assets/js/common'
	export default {
		name: 'bar',
		mixins: [chartMixin],
		props: {
			sourceDistributeData: {
				type: Object,
				default: () => {}
			}
		},
		data() {
			return {}
		},
		watch: {
			sourceDistributeData: {
				handler: function(val, oldVal) {
					this.initChart()
				},
				deep: true //增加deep 观察对象的子对象变化
			}
		},
		mounted() {
			this.$nextTick(() => {
//				console.log(this.sourceDistributeData)
				this.myChart = this.$echarts.init(this.$refs.chart);
				this.initChart()
			})
		},
		methods: {
			initChart() {
				let option = {
					color: ['#3398DB'],
					tooltip: {
						trigger: 'axis',
						axisPointer: {
							type: 'shadow'
						},
						backgroundColor:"rgba(255,255,255,0.8)",
						textStyle:{
							color:"#474747",
							fontWeight:'bold'
						},
						formatter:'{b}: {c}'
					},
					grid: {
						left: '4%',
						right: '4%',
						bottom: '0%',
						top: "4%",
						containLabel: true
					},
					xAxis: {
						show:false
					},
					yAxis: [{
						type: 'category',
						name: '',
						inverse:true,
						axisTick: {
							show: false //坐标轴刻度
						},
						axisLine: {
							show: true,
							lineStyle: {
								color: "#0592EA",
							}
						},
						axisLabel: {
							textStyle: {
								color: '#fff',
								fontSize: 12,
							},
						},
						splitLine: {
							lineStyle: {
								color: '#166DD4',
								type: "dashed",

							}
						},
						data: this.sourceDistributeData.area,
					}],
					series: [{
						name: '来源分布',
						type: 'bar',
						barWidth: 14,
						direction:"90deg",
						showSymbol: false,
						smooth: true,
						itemStyle: {
								normal: {
									color: "rgba(23,158,219,0.75)",
									borderColor:"rgba(0,174,255,0.75)",
									label:{
											show:true,
											position:"right",
										},
								},
								emphasis: {
									color: "rgba(232,221,33,0.95)",
									borderColor:"rgba(255,228,0,0.95)",
									label:{
											show:true,
											color: "rgba(232,221,33,0.95)",
											position:"right",
										},
								}
							},
						data: this.sourceDistributeData.distribute
					}]
				}
				this.myChart.on('click', (params) => {
					this.$emit('handleCharts', params.name)
				})
				this.myChart.setOption(option);

			},
      // echats 图表自适应
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



