<template>
	<div class="chart-wrapper" ref="ageORsex" style="height:100%;width:100%">

	</div>
</template>

<script>
	import {chartMixin} from 'assets/js/common'
	export default {
		name: 'bar',
		mixins: [chartMixin],
		props: {
			ageORsexData: {
				type: Object,
				default: () => {}
			}
		},
		data() {
			return {}
		},
		watch: {
			ageORsexData: {
				handler: function(val, oldVal) {
					this.initChart()
				},
				deep: true //增加deep 观察对象的子对象变化
			}
		},
		mounted() {
			this.$nextTick(() => {
//				console.log(this.ageORsexData)
				this.myChart = this.$echarts.init(this.$refs.ageORsex);
				this.initChart()
			})
		},
		methods: {
			initChart() {
				let option = {
					color: ['#3398DB'],
					tooltip: {
						trigger: 'axis',
						axisPointer: {
							type: 'shadow'
						},
						backgroundColor:"rgba(255,255,255,0.8)",
						textStyle:{
							color:"#474747",
							fontWeight:'bold'
						},
						formatter:(params)=>{
							console.log(params)
							let html = '<div>'     
							params.forEach((val,index)=>{
								html+=`${val.seriesName}:${val.data}<br/>`
							})
							return html+'</div>'
						}
					},
					grid: {
						left: '3%',
						right: '4%',
						bottom: '9%',
						top: "14%",
						containLabel: true
					},
					xAxis: [{
						type: 'category',
						data: this.ageORsexData.age,
						boundaryGap: true, //坐标轴的留白
						axisLine: {
							show: true,
							lineStyle: {
								color: "#0592EA",
							}
						},
						axisTick: {
							show: false //坐标轴刻度
						},
						axisLabel: {
							textStyle: {
								color: '#fff',
								fontSize: 12,
							},
						},
					}],
					yAxis: {
						type: 'value',
						name: '',
						axisTick: {
							show: false //坐标轴刻度
						},
						axisLine: {
							show: false,
						},
						axisLabel: {
							show: false,
						},
						splitLine: {
							lineStyle: {
								color: '#166DD4',
								type: "dashed",

							}
						},
					},
					legend: {
						data: ["男", "女"],
						color: "#099D4F",
						itemWidth: 14,
						itemHeight: 7,
						itemGap: 28,
						icon: "rect",
						textStyle: {
							color: "#fff",
							fontSize: 12,
						},
						top: 0,
					},
					series: [{
							name: '男',
							type: 'bar',
							stack: "true",
							barWidth: 14,
							showSymbol: false,
							smooth: true,
							itemStyle: {
								normal: {
									color: "rgba(23,158,219,0.55)",
									borderColor:"rgba(0,174,255,0.55)"
								},
							},
							data: this.ageORsexData.man
						},
						{
							name: '女',
							type: 'bar',
							barWidth: 14,
							stack: "true",
							smooth: true,
							showSymbol: false,
							itemStyle: {
								normal: {
									color: "rgba(232,193,26,0.95)",
									borderColor:"rgba(255,228,0,0.95)"
								},
							},
							data: this.ageORsexData.woman
						},
					]
				}
				this.myChart.on('click', (params) => {
					this.$emit('handleCharts', params.name)
				})
				this.myChart.setOption(option);

			},
      // echats 图表自适应
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



