<template>
  <div class="big" :class="{'container-wrapper': show}">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: "containers",
    props: {
      show: {
        type: Boolean,
        default: true
      }
    }
  }
</script>

<style lang="scss" scoped>
  /*@import "../assets/css/_variable.scss";*/
  /*@import "../assets/css/_mixin.scss";*/
  .big{
  	width: 100%;
    /*height: 100%;*/
  }
  .container-wrapper {
    color: #fff;
    width: 1200px;
    margin: 20px auto;
    background-color: rgba(9, 5, 45, .7);
    padding: 20px 0;
    box-sizing: border-box;
    border-radius: 10px;
  }
</style>



