  width: 90%;
  border: 1px solid #d4d4d4;
  border-radius: 3px;
  height: 22px;
  outline: none;
  padding-right: 30px;
  box-sizing: border-box;
}
.detail-box  .input-box  .icon-search{
  font-size: 20px;
  width: 30px;
  color: #d4d4d4;
  height: 22px;
  right: 18px;
  top: 2px;
}
.city-list{
  overflow: hidden;
  border-bottom:1px solid #0352dd;
  padding: 3px 15px;
  line-height: 32px;
}
.city-list-top span{
  cursor: pointer;
  color: #ffffff;
}

.city-list-top span:hover{
   text-decoration: underline;
 }
  .city-list-bottom a{
    color: #ffffff;
  }
.city-list-bottom span{
  padding: 0 2px;
  color: #ffffff;
  cursor: pointer;
}
.city-list-bottom span:hover{
   text-decoration: underline;
 }
.city-list-detail{
  padding: 0 5px;
  height: 173px;
  box-sizing: border-box;
  padding-top: 12px;
  overflow-y: scroll;
/*滚动条样式*/
}

.city-list-detail::-webkit-scrollbar{width:6px;height:10px;}
.city-list-detail::-webkit-scrollbar-thumb{ background:rgba(204,204,204,1); border-radius: 5px;}
.city-list-detail-content{
  height: 300px;
}
.city-list-detail-content li {
  overflow: hidden;
}
.city-list-detail-content li span{
  color: #ffffff;
  padding: 0 7px;
  cursor: pointer;}

.city-list-detail-content li span:hover{text-decoration: underline;}
.city-list-detail-content .detail-left{
  width: 30px;
  font-size: 20px;
  color: #cccccc;
  text-indent: 6px;
  text-align: center;
}
.city-list-detail-content .detail-middle{
  width: 60px;
}
.city-list-detail-content .detail-middle span{
  padding: 0;
}
.city-list-detail-content .detail-right{
  width: 210px;
}
  .list-container{
    margin-top: 20px;
    padding: 0 20px;
  }
 .list-container .btn-warp{
    overflow: hidden;
    margin-bottom: 20px;
  }
  .list-container  .btn-warp li{
    float: left;
    width: 80px;
    height: 24px;
    line-height: 24px;
    margin-right:20px;
    -webkit-border-radius:3px;
    -moz-border-radius:3px;
    border-radius:3px;
    text-align: center;
    font-size: 14px;
    cursor: pointer;
  }
  .list-container .btn-warp li.start{
    background:#072372;
    border: solid 1px #1850a4;
    color: #bddcff;
  }
  .list-container .btn-warp li.delete{
    border: solid 1px #84231C;
    background:#321e51;
    color: #ffbdbd;
  }
  .list-container .btn-warp li.stop{
    border: solid 1px rgba(46, 94, 16, 1);
    background:#123659;
    color: #c1ff8f;
  }
  .list-container .btn-warp li.daochu{
    border: solid 1px rgba(105, 82, 12, 1);
    background:#1e3058;
    color: #ffe68f;
  }
  .list-container  .btn-warp li span{
    margin-right: 2px;
    font-size: 14px;
  }
  .list-container .table-wrap {
    width: 100%;
    padding-bottom: 20px;
  }
  .list-container .table-list {
    width: 100%;
  }
  .list-container .table-list tr, .list-container .table-list th{
    height: 40px;
    line-height: 40px;
    box-sizing: border-box;
    text-align: center;
  }
  .list-container .table-list tr {
    border-bottom: 1px #29328f solid;
    transition: all .2s ease-in;
  }
  .list-container .table-list tr:hover {
     background-color: #29328f;
   }
  .list-container .table-list tr:nth-child(1) {
     border-top: 1px #29328f solid;
   }
  .list-container .table-list thead {
    color: #6b759f;
  }
  .list-container  .icon-delete{
    color: #943427;
    cursor: pointer;
    font-size:20px;
    font-weight: 700;
  }
  .list-container .icon-bj{
    color: #337823;
    cursor: pointer;
    font-size:20px;
    margin-right:20px;
    font-weight: 700;
  }
  .list-container .icon-Success-Small{
    color: #034e93;
    cursor: pointer;
    font-size:16px;
    margin-right:20px;
    font-weight: 700;
  }
  .list-container .icon-tingyong{
    color: #896b0b;
    cursor: pointer;
    font-size:16px;
    margin-right:20px;
    font-weight: 700;
  }
</style>

<style>

  .el-checkbox__inner{
    background: #0b1e6b;
    border: solid 1px rgba(44, 79, 210, 1);
  }
  .BMap_pop>img{
    display: none;
  }
  .isDelete{
    text-align: center;
  }

</style>




