<template>
  <div class="early-wrap">
    <p class="search-info">根据可疑物品的历史数据分析，预测某一时间段内物品收发数量，或收发地点。</p>
    <div class="table-wrap">
      <table class="table-list">
        <thead>
          <tr>
            <th width="3%">序号</th>
            <th width="7%">物品名称</th>
            <th width="7%">疑似物品</th>
            <th width="12%">发货地点</th>
            <th width="5%">发货人</th>
            <th width="12%">收货地点</th>
            <th width="5%">收货人</th>
            <th width="8%">快递公司</th>
            <th width="8%">网站名称</th>
            <th width="7%">手机号</th>
            <th width="12%">预计购买开始时间</th>
            <th width="12%">预计购买截至时间</th>
          </tr>
        </thead>
        <tbody v-for="(item,index) in list">
        <tr>
          <td>{{item.id}}</td>
          <td>{{item.name}}</td>
          <td>{{item.suspectedItems}}</td>
          <td>{{item.deliverAddress}}</td>
          <td>{{item.deliver}}</td>
          <td>{{item.shipAddress}}</td>
          <td>{{item.shiper}}</td>
          <td>{{item.expressage}}</td>
          <td>{{item.website}}</td>
          <td>{{item.phone}}</td>
          <td>{{item.startTime}}</td>
          <td>{{item.endTime}}</td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="mgt-20">
      <pagination :curIndex.sync="curIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
  </div>
</template>

<script>
import datepicker from "components/datepicker";
import pagination from "components/pagination";
import { earlywarning as api } from "../../api/earlywarning";
export default {
  name: "suspiciousarticle",
  data() {
    return {
      // 两种方式都能返回当前页面
      earlyNum: 120,
      curIndex: 1,
      pageData: {
        curPage: 1,
        pageSize: 10,
        total: 0 // 必须传
      },
      list: []
    };
  },
  methods: {
    goSearch() {
      // go search
      // console.log("search");
    },
    changePage(index) {
      this.getList({ pageIndex: index });
    },
    getList(params) {
      if (params && params.pageIndex) {
        this.curIndex = params.pageIndex;
      }
      let param = params || {};
      param.pageIndex = this.curIndex;
      param.pageSize = this.pageData.pageSize;
      // console.info(param);
      api.getList(params).then(res => {
        if (res.code && res.code === 1) {
          // console.log(res.data.list);
          this.list = res.data.list;
          this.earlyNum = res.data.earlyNum;
          this.pageData.curPage = this.curIndex;
          this.pageData.total = res.data.total;
        }
      });
    }
  },
  components: {
    pagination,
    datepicker
  },
  mounted() {
    this.getList();
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/variable";
@import "../../assets/css/mixin";

.early-wrap {
  padding:0 20px;
  .date-picker-wrap {
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .date-picker-left {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      span:nth-child(1) {
        color: #c2cae2;
        font-size: $fz-sm;
        font-weight: bold;
        margin-right: 20px;
      }
      .search {
        font-size: $fz-sm;
        line-height: 28px;
        text-align: center;
        background: linear-gradient(to right, #0095ff, #2e5ffc);
        border-radius: 2px;
        width: 90px;
        height: 28px;
        margin-left: 20px;
        transition: all 0.2s ease-in;
        &:hover {
          cursor: pointer;
          box-shadow: 0 0 2px #00bdff;
        }
      }
    }
    .date-picker-right {
      color: #a3d8ff;
      font-size: $fz-sm;
      cursor: pointer;
      transition: all 0.2s ease-in;
      &:hover {
        color: #fff;
      }
    }
  }
  .check-list {
    width: 100%;
    /*height: 30px;*/
    /*line-height: 30px;*/
    margin-top: 30px;
  }
  .search-info {
    width: 100%;
    height: 30px;
    line-height: 30px;
    margin-top: 30px;
    border-radius: 2px;
    padding-left: 14px;
    background: linear-gradient(
      to right,
      rgba(8, 39, 135, 0.9),
      rgba(8, 39, 135, 0)
    );
    span {
      color: #ffd618;
    }
  }
  .table-wrap {
    width: 100%;
    padding: 30px 0;
    .table-list {
      width: 100%;
      tr,
      th {
        height: 40px;
        line-height: 40px;
        box-sizing: border-box;
        text-align: center;
      }
      tr {
        border-bottom: 1px #29328f solid;
        transition: all 0.2s ease-in;
        &:hover {
          background-color: #29328f;
        }
        &:nth-child(1) {
          border-top: 1px #29328f solid;
        }
        td {
          overflow: hidden;
          text-overflow: ellipsis;
          // white-space: nowrap;
        }
      }
      thead {
        color: #6b759f;
      }
    }
  }
}
</style>



