<template>
  <div>
    <div class="dropdown">
      <span>统计周期 :</span>
      <el-select v-model="value" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div class="box">
      <div>
        <table class="table">
          <thead>
          <tr>
            <td>序号</td>
            <td>游戏类别</td>
            <td>游戏名称</td>
            <td>游戏时长预估</td>
            <td>游戏时段偏好/td>
            <td>游戏工具</td>
            <td>备注</td>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item,index) in list">
            <td>{{index+1}}</td>
            <td>{{item.yxlb}}</td>
            <td>{{item.yxmc | isValue}}</td>
            <td>{{item.scyg | isValue}}</td>
            <td>{{item.sdph | isValue}}</td>
            <td>{{item.yxgj | isValue}}</td>
            <td>-</td>
          </tr>
          </tbody>
        </table>
        <div class="pagination-wrap">
          <pagination :currentIndex.sync="currentIndex" :pageData="pageData" @changePage="changePage"></pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  import pagination from 'components/pagination';
  export default {
    name: 'game',
    data(){
      return {
        list:[
          {yxlb:'暴力',yxmc:'生化危机',scyg:'30小时',sdph:'19:00-01:00',yxgj:'电脑'},
          {yxlb:'',yxmc:'',scyg:'',sdph:'',yxgj:''},
          {yxlb:'',yxmc:'',scyg:'',sdph:'',yxgj:''},
          {yxlb:'',yxmc:'',scyg:'',sdph:'',yxgj:''},
          {yxlb:'',yxmc:'',scyg:'',sdph:'',yxgj:''},
          {yxlb:'',yxmc:'',scyg:'',sdph:'',yxgj:''},
          {yxlb:'',yxmc:'',scyg:'',sdph:'',yxgj:''},
          {yxlb:'',yxmc:'',scyg:'',sdph:'',yxgj:''},
          {yxlb:'',yxmc:'',scyg:'',sdph:'',yxgj:''},
          {yxlb:'',yxmc:'',scyg:'',sdph:'',yxgj:''},
        ],
        // 两种方式都能返回当前页面
        currentIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 70,  // 必须传
        },
        options:[
          {value:1,label:'最近一周'}
        ],
        value:''
      }
    },
    methods:{
      changePage(val) {
        // 改变页码
        console.log(val)
        console.log(this.currentIndex)
      },
    },
    filters:{
      isValue(val) {
        return (val = val == "" ? "-" : val);
      }
    },
    components: {
      pagination,
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";


  .dropdown{
    margin-top: 63px;
  }
  .box>div:nth-child(1){
    width: 100%;
    float: left;
    padding-right: 1%;
    margin-top: 30px;
  }
  .table{
    width: 100%;
    float: left;
    thead{
      color: #8388a4;
      border-top: 1px solid #0d3196;
    }
    tr{
      border-bottom: 1px solid #0d3196;
      line-height: 38px;
      text-align: center;
    }
    tbody>tr:hover{
      background-color: #0d3196;
    }
  }
  .pagination-wrap{
    float: right;
    margin-top:15px;
    width: 92%;
  }
</style>



