<template>
  <div class="date-picker">
    <el-date-picker
      v-model="startTime"
      type="date"
      :editable=false
      placeholder="开始日期"
      :picker-options="pickerOptions0"
      @change="changeDateS">
    </el-date-picker>
    -
    <el-date-picker
      v-model="endTime"
      type="date"
      :editable=false
      placeholder="结束日期"
      :picker-options="pickerOptions1"
      @change="changeDateE">
    </el-date-picker>
  </div>
</template>

<script>
  export default {
    name: "datepicker",
    data() {
      return {
        startTime: '',
        endTime: '',
        pickerOptions0: {
          disabledDate: (time) => {
            if (this.endTime != "") {
              return time.getTime() > Date.now() || time.getTime() > this.endTime;
            } else {
              return time.getTime() > Date.now();
            }

          }
        },
        pickerOptions1: {
          disabledDate: (time) => {
            return time.getTime() < this.startTime || time.getTime() > Date.now();
          }
        },

      }
    },
    props: {
      defaultDate: {
        type: Object,
        default: () => {

        }
      }
    },
    mounted() {
      this.initDate()
      this.$emit('changDate', this.dateRange)
    },
    computed: {
      dateRange: function () {
        return {
          start: this.formatDate(this.startTime, 'yyyy-MM-dd'),
          end: this.formatDate(this.endTime, 'yyyy-MM-dd')
        }
      }
    },
    methods: {
      formatDate(date,fmt) {
        if(date === null){
            return
        }
        let that = new Date(date)
          let o = {
            "M+": that.getMonth() + 1, //月份
            "d+": that.getDate(), //日
            "h+": that.getHours(), //小时
            "m+": that.getMinutes(), //分
            "s+": that.getSeconds(), //秒
            "q+": Math.floor((that.getMonth() + 3) / 3), //季度
            "S": that.getMilliseconds() //毫秒
          };
          if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (that.getFullYear() + "").substr(4 - RegExp.$1.length));
          for (var k in o)
            if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
          return fmt;
      },
      initDate() {
        if (this.defaultDate && Object.keys(this.defaultDate).length) {
          this.startTime = this.defaultDate.start
          this.endTime = this.defaultDate.end
        } else {
          this.startTime = Date.now() - 1000 * 60 * 60 * 24 * 7
          this.endTime = Date.now()
        }
      },
      changeDateS(val) {
        // this.dateRange.start = val
        this.$emit('changDate', this.dateRange)
        // console.log(val)
      },
      changeDateE(val) {
        // this.dateRange.end = val
        this.$emit('changDate', this.dateRange)
        // console.log(val)
      },
      clearDate() {
        this.startTime = ''
        this.endTime = ''
      }
    }
  }
</script>

<style lang="scss">
  @import "../assets/css/variable";
  @import "../assets/css/mixin";

  .date-picker {
    color: #0141b5;
    .el-input__inner {
      color: #fff;
      height: 30px;
      border: 1px #0141b5 solid;
      background-color: transparent;
      &:hover {
        border-color: #409eff;
      }
      &:focus {
        border-color: #409eff;
      }
    }
    .el-input__prefix, .el-input__suffix {
      .el-input__icon {
        height: 30px;
        line-height: 30px;
      }
    }
    .el-picker-panel {
      border: 1px #0141b5 solid;
      background: #000;
    }
  }

  // 时间选择框的样式
  .el-picker-panel {
    border: 1px #0141b5 solid !important;
    background: #0a2369 !important;
    .el-picker-panel__body {
      .el-date-picker__header {
        .el-picker-panel__icon-btn {
          color: #fff;
        }
        .el-date-picker__header-label {
          color: #fff;
        }
      }
      .el-picker-panel__content {
        .el-date-table {
          color: #fff;
          th {
            color: #fff;
          }
          td.disabled div {
            background-color: rgba(102, 102, 102, .5);
            color: #777;
          }
        }
      }
    }
  }

  .el-popper[x-placement^=bottom] {
    .popper__arrow {
      border-bottom-color: #0141b5;
    }
    .popper__arrow::after {
      border-bottom-color: #0a2369;
    }

  }


</style>



