import suspiciouslocation from "pages/suspiciouslocation/suspiciouslocation";
import siteAnalyze from "pages/suspiciouslocation/siteAnalyze";
import emphasisAreaAnalyze from "pages/suspiciouslocation/emphasisAreaAnalyze";
export default [
  {
    path: '/suspiciouslocation',
    name: 'suspiciouslocation',
    component: suspiciouslocation,
    meta: {
      title: '可疑地点分析-猎狐大数据'
    },
    redirect: '/suspiciouslocation/siteAnalyze',
    children: [
      {
        path: 'siteAnalyze',
        name: 'siteAnalyze',
        component: siteAnalyze,
        meta: {
          title: '可疑地点分析-可疑地点分析-猎狐大数据'
        }
      },
      {
        path: 'emphasisAreaAnalyze',
        name: 'emphasisAreaAnalyze',
        component: emphasisAreaAnalyze,
        meta: {
          title: '可疑地点分析-重点区域分析-猎狐大数据'
        }
      }
    ]
  }]



