<template>
    <div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

    </div>
</template>

<script>
  export default {
  name: 'bar',
  props: {
    echartsData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {}
  },
  mounted() {
    this.$nextTick(()=>{
      this.myChart = this.$echarts.init(this.$refs.chart);
      this.initChart()
    })
  },
  methods: {
   initChart() {
      var indicatorData = [
        {text: '涉黄', max: 100},
        {text: '杀人', max: 100},
        {text: '强奸', max: 100},
        {text: '涉赌', max: 100},
        {text: '涉毒', max: 100}]

      var indicatorDataOther = []
      var data = [77, 25, 68, 61, 60]
      var sum = data.reduce(function (a, b) {
        return a + b
      })
      data.map(function (val) {
        var item = {text: ((val / sum) * 100).toFixed(1) + '%', max: 100}
        indicatorDataOther.push(item)
      })

      var option = {
        tooltip: {
          formatter: function (val) {
            var hint = val.seriesName + '<br>'
            for (var i = 0; i < val.value.length; i++) {
              hint += indicatorData[i].text + '\t:\t' + val.value[i] + '<br>'
            }
            return hint;
          }
        },
        radar: [
          {
            indicator: indicatorData,
            center: ['52%', '55%'],
            radius: 60,
            splitNumber: 3,
            name: {
              textStyle: {color: '#fff', fontSize: 14, padding: [0, 44, 0, 0]},
              formatter: '{value}\t'
            },
            splitArea: {
              areaStyle: {
                color: ['#0b2c90', '#0c37ae']
              }
            },
            axisLine: {
              lineStyle: {
                color: '#1372f2'
              }
            },
            axisLabel: {
              textStyle: {
                color: 'yellow'
              }
            },
            splitLine: {
              lineStyle: {
                color: '#1372f2'
              }
            }
          },
          {
            indicator: indicatorDataOther,
            center: ['52%', '55%'],
            radius: 60,
            silent:true,
            splitNumber: 3,
            name: {
              textStyle: {color: '#ffd800', fontSize: 14}, padding: [0, 0, 0, 33]
            },
            splitArea: {
              areaStyle: {
                color: ['#0b2c90', '#0c37ae']
              }
            },
            axisLine: {
              lineStyle: {
                color: '#1372f2'
              }
            },
            axisLabel: {
              textStyle: {
                color: 'yellow'
              }
            },
            splitLine: {
              lineStyle: {
                color: '#1372f2'
              }
            },
            z:-1
          }
        ],
        series: [
          {
            name: '人物画像',
            type: 'radar',
            symbol: 'none',
            data: [
              {
                value: data,
                name: '',
                symbolSize: 8,
                itemStyle: {
                  normal: {
                    color: '#007cfe'
                  }
                },
                areaStyle: {
                  normal: {
                    color: 'rgba(60,234,233,1)'
                  }
                }
              }
            ]
          }
        ]
      }

      this.myChart.setOption(option);
    }

  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



