<template>
  <div>
    <table class="table">
      <thead>
      <tr>
        <td>序号</td>
        <td>入学时间</td>
        <td>毕业时间</td>
        <td>学校名称</td>
        <td>专业</td>
        <td>学历</td>
        <td>学校地址</td>
        <td>证明人</td>
        <td>证明人联系方式</td>
        <td>备注</td>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item,index) in list">
        <td>{{index+1}}</td>
        <td>{{item.rxsj | isValue}}</td>
        <td>{{item.bysj | isValue}}</td>
        <td>-</td>
        <td>-</td>
        <td>-</td>
        <td>-</td>
        <td>-</td>
        <td>-</td>
        <td>-</td>
      </tr>
      </tbody>
    </table>
    <div class="pagination-wrap">
      <pagination :currentIndex.sync="currentIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
  </div>
</template>

<script type="text/javascript">
  import pagination from 'components/pagination';
  export default {
    name: 'culture',
    data(){
      return {
        list:[
          {rxsj:'2002-09-01',bysj:'2018-09-01'},
          {rxsj:'2002-09-01',bysj:'2018-09-01'},
          {rxsj:'2002-09-01',bysj:'2018-09-01'},
          {rxsj:'2002-09-01',bysj:'2018-09-01'},
          {rxsj:'2002-09-01',bysj:'2018-09-01'},
          {rxsj:'',bysj:''},
          {rxsj:'',bysj:''},
          {rxsj:'',bysj:''},
          {rxsj:'',bysj:''},
          {rxsj:'',bysj:''},
        ],
        // 两种方式都能返回当前页面
        currentIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 70,  // 必须传
        }
      }
    },
    methods:{
      changePage(val) {
        // 改变页码
        console.log(val)
        console.log(this.currentIndex)
      },
    },
    filters:{
      isValue(val) {
        return (val = val == "" ? "-" : val);
      }
    },
    components: {
      pagination,
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/variable";
  @import "~assets/css/mixin";

  .table{
    width: 100%;
    float: left;
    margin-top: 30px;
    thead{
      color: #8388a4;
      border-top: 1px solid #0d3196;
    }
    tr{
      border-bottom: 1px solid #0d3196;
      line-height: 38px;
      text-align: center;
    }
    tbody>tr:hover{
      background-color: #0d3196;
    }
  }
  .pagination-wrap{
    float: right;
    margin-top:15px;
  }
</style>



