<template>
    <div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

    </div>
</template>

<script>
	import {geoCoordMap,convertData} from 'assets/js/chartData.js';
    import shandongJson from "../../../../static/json/370000.json"
    import {chartMixin} from 'assets/js/common'
  export default {
  name: 'bar',
  mixins: [chartMixin],
  data() {
    return {}
  },
  mounted() {
    this.$nextTick(()=>{
         this.myChart = this.$echarts.init(this.$refs.chart);
      this.initChart()
    })
  },
  methods: {
   initChart() {
   	this.$echarts.registerMap('shandong', shandongJson);
      let option = {
        tooltip: {
            trigger: 'item',
			backgroundColor:'#303030',
			show:false
        },

        series: {
        	type:'map',
        	map:"shandong",
        	itemStyle:{
        		normal:{
        			borderColor:"#0858be",
        			borderWidth:1,
        			areaColor:"#02a9f4",
        			shadowColor: 'rgba(255, 255, 255, 0.8)',
                    shadowBlur: 2
        		}
        	}
        }
    }
      this.myChart.setOption(option);

   },
    // echats 图表自适应
    _windowResizeHandler() {
      this.myChart.resize()
    },
    // 销毁echats图表方法
    _destroyEchart() {
      if (this.myChart) {
        this.myChart.dispose()
      }
    }
  },
}
</script>


<style lang="scss" scoped>

</style>



