<template>
  <div ref="mapChart" class="mapChart-wrapper" style="height:730px;width:100%">
  </div>
</template>
<script>
  import {geoCoordMap,convertData} from 'assets/js/chartData.js';
  import {chartMixin} from 'assets/js/common.js';

  // require('echarts/lib/chart/map')
  require('echarts/map/js/china.js')
  // require('echarts/map/json/china.json')
  export default {
    name: 'mapecharts',
    mixins: [chartMixin],
    data() {
      return {
        myChart: null,
      }
    },

    props: {},

    watch: {
      echartsData: {
        handler: function (val, oldVal) {
          // this.initChart()
        },
        deep: true //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.myChart = this.$echarts.init(this.$refs.mapChart)
      this.initChart()
    },
    computed: {},
    methods: {
      initChart() {
        let BJData= [
          [{name:'北京'}, {name:'上海',value:95}],
          [{name:'北京'}, {name:'广州',value:90}],
          [{name:'北京'}, {name:'大连',value:80}],
          [{name:'北京'}, {name:'南宁',value:70}],
          [{name:'北京'}, {name:'南昌',value:60}],
          [{name:'北京'}, {name:'拉萨',value:50}],
          [{name:'北京'}, {name:'长春',value:40}],
          [{name:'北京'}, {name:'包头',value:30}],
          [{name:'北京'}, {name:'重庆',value:20}],
          [{name:'北京'}, {name:'常州',value:10}]
        ],

          SHData= [
          [{name:'上海'},{name:'包头',value:95}],
          [{name:'上海'},{name:'昆明',value:90}],
          [{name:'上海'},{name:'广州',value:80}],
          [{name:'上海'},{name:'郑州',value:70}],
          [{name:'上海'},{name:'长春',value:60}],
          [{name:'上海'},{name:'重庆',value:50}],
          [{name:'上海'},{name:'长沙',value:40}],
          [{name:'上海'},{name:'北京',value:30}],
          [{name:'上海'},{name:'丹东',value:20}],
          [{name:'上海'},{name:'大连',value:10}]
        ],

          GZData= [
          [{name:'广州'},{name:'福州',value:95}],
          [{name:'广州'},{name:'太原',value:90}],
          [{name:'广州'},{name:'长春',value:80}],
          [{name:'广州'},{name:'重庆',value:70}],
          [{name:'广州'},{name:'西安',value:60}],
          [{name:'广州'},{name:'成都',value:50}],
          [{name:'广州'},{name:'常州',value:40}],
          [{name:'广州'},{name:'北京',value:30}],
          [{name:'广州'},{name:'北海',value:20}],
          [{name:'广州'},{name:'海口',value:10}]
        ];
        var color = ['#a6c84c', '#ffa022', '#46bee9'];
        var planePath = 'path://M1705.06,1318.313v-89.254l-319.9-221.799l0.073-208.063c0.521-84.662-26.629-121.796-63.961-121.491c-37.332-0.305-64.482,36.829-63.961,121.491l0.073,208.063l-319.9,221.799v89.254l330.343-157.288l12.238,241.308l-134.449,92.931l0.531,42.034l175.125-42.917l175.125,42.917l0.531-42.034l-134.449-92.931l12.238-241.308L1705.06,1318.313z';
        var series = [];
        [['北京', BJData], ['上海', SHData], ['广州', GZData]].forEach(function (item, i) {
          series.push({
              name: item[0] + ' Top10',
              type: 'lines',
              zlevel: 1,
              effect: {
                show: true,
                period: 6,
                trailLength: 0.7,
                color: '#fff',
                symbolSize: 3
              },
              lineStyle: {
                normal: {
                  color: color[i],
                  width: 0,
                  curveness: 0.2
                }
              },
              data: convertData(item[1])
            },
            {
              name: item[0] + ' Top10',
              type: 'lines',
              zlevel: 2,
              symbol: ['none', 'arrow'],
              symbolSize: 10,
              effect: {
                show: true,
                period: 6,
                trailLength: 0,
                // symbol: planePath,
                symbolSize: 3
              },
              lineStyle: {
                normal: {
                  color: color[i],
                  width: 1,
                  opacity: 0.6,
                  curveness: 0.2
                }
              },
              data: convertData(item[1])
            },
            {
              name: item[0] + ' Top10',
              type: 'effectScatter',
              coordinateSystem: 'geo',
              zlevel: 2,
              rippleEffect: {
                brushType: 'stroke'
              },
              label: {
                normal: {
                  show: true,
                  position: 'right',
                  formatter: '{b}'
                }
              },
              symbolSize: function (val) {
                return val[2] / 8;
              },
              itemStyle: {
                normal: {
                  color: color[i]
                }
              },
              data: item[1].map(function (dataItem) {
                return {
                  name: dataItem[1].name,
                  value: geoCoordMap[dataItem[1].name].concat([dataItem[1].value])
                };
              })
            });
        });

        let option = {
          backgroundColor: '#0b134a',
          tooltip : {
            trigger: 'item'
          },
          legend: {
            orient: 'vertical',
            top: 'bottom',
            left: 'right',
            data:['北京 Top10', '上海 Top10', '广州 Top10'],
            textStyle: {
              color: '#fff'
            },
            selectedMode: 'single'
          },
          geo: {
            map: 'china',
            zoom: 1.3,
            label: {
              emphasis: {
                show: false
              }
            },
            roam: true,
            itemStyle: {
              normal: {
                areaColor: '#092d75',
                borderColor: '#104c94'
              },
              emphasis: {
                areaColor: '#104c94'
              },
              borderType: 'dashed'
            }
          },
          series: series
        };
        this.myChart.setOption(option);
      },
      // echats 图表自适应
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
    }
  }

</script>
<style lang="scss" scoped>

  .mapChart-wrapper {
    width: 100%;
    height: 100%;
    /*border-radius: 5px;*/
    border: 1px #29328f solid;
  }

</style>



