import suspiciousperson from "pages/suspiciousperson/suspiciousperson";
import personCheck from "pages/suspiciousperson/personCheck";
import personStats from "pages/suspiciousperson/personStats";

/**
 * 可疑人员查询--活动轨迹
 */
const trackIndex = r => require.ensure([], () => r(require('pages/suspiciousperson/trackDetail/index')), 'trackDetail')
const keyArea = r => require.ensure([], () => r(require('pages/suspiciousperson/trackDetail/keyArea')), 'trackDetail')
const internetCafes = r => require.ensure([], () => r(require('pages/suspiciousperson/trackDetail/internetCafes')), 'trackDetail')
const hotelDetail = r => require.ensure([], () => r(require('pages/suspiciousperson/trackDetail/hotelDetail')), 'trackDetail')
const visitUyghurNet = r => require.ensure([], () => r(require('pages/suspiciousperson/trackDetail/visitUyghurNet')), 'trackDetail')
const visitTerrorismNet = r => require.ensure([], () => r(require('pages/suspiciousperson/trackDetail/visitTerrorismNet')), 'trackDetail')
const emailDetail = r => require.ensure([], () => r(require('pages/suspiciousperson/trackDetail/emailDetail')), 'trackDetail')
const useApp = r => require.ensure([], () => r(require('pages/suspiciousperson/trackDetail/useApp')), 'trackDetail')

export default [
  {
    path: '/suspiciousperson',
    name: 'suspiciousperson',
    component: suspiciousperson,
    meta: {
      title: '可疑人员分析-猎狐大数据'
    },
    redirect: '/suspiciousperson/personCheck',
    children: [
      {
        path: 'personCheck',
        name: 'personCheck',
        component: personCheck,
        meta: {
          title: '可疑人员分析-可疑人员查询-猎狐大数据'
        }
      },
      {
        path: 'personCheck/trackDetail/:id',
        name: 'trackIndex',
        component: trackIndex,
        meta: {
          title: '可疑人员分析-可疑人员查询-猎狐大数据'
        },
        children: [
          {
            path: '',
            redirect: 'keyArea'
          },
          {
            path: 'keyArea',
            name: 'keyArea',
            component: keyArea,
            meta: {
              title: '可疑人员分析-可疑人员查询-重点区域详情-猎狐大数据'
            }
          },
          {
            path: 'internetCafes',
            name: 'internetCafes',
            component: internetCafes,
            meta: {
              title: '可疑人员分析-可疑人员查询-网吧详情-猎狐大数据'
            }
          },
          {
            path: 'hotelDetail',
            name: 'hotelDetail',
            component: hotelDetail,
            meta: {
              title: '可疑人员分析-可疑人员查询-宾馆详情-猎狐大数据'
            }
          },
          {
            path: 'visitUyghurNet',
            name: 'visitUyghurNet',
            component: visitUyghurNet,
            meta: {
              title: '可疑人员分析-可疑人员查询-访问维文网站-猎狐大数据'
            }
          },
          {
            path: 'visitTerrorismNet',
            name: 'visitTerrorismNet',
            component: visitTerrorismNet,
            meta: {
              title: '可疑人员分析-可疑人员查询-访问涉恐网站-猎狐大数据'
            }
          },
          {
            path: 'emailDetail',
            name: 'emailDetail',
            component: emailDetail,
            meta: {
              title: '可疑人员分析-可疑人员查询-邮件详情-猎狐大数据'
            }
          },
          {
            path: 'useApp',
            name: 'useApp',
            component: useApp,
            meta: {
              title: '可疑人员分析-可疑人员查询-使用APP详情-猎狐大数据'
            }
          }
        ]
      },
      {
        path: 'personStats',
        name: 'personStats',
        component: personStats,
        meta: {
          title: '可疑人员分析-可疑人员统计-猎狐大数据'
        }
      }
    ]
  }]



