import setting2 from "pages/setting/setting2";
import virtualPortrait from "pages/setting/portrait/virtualPortrait";
import realisticPortrait from "pages/setting/portrait/realisticPortrait";
import family from "pages/setting/portrait/personalData/family";
import culture from "pages/setting/portrait/personalData/culture";
import medical from "pages/setting/portrait/personalData/medical";
import work from "pages/setting/portrait/personalData/work";
import shopping from "pages/setting/portrait/personalData/shopping";
import communication from "pages/setting/portrait/personalData/communication";
import sports from "pages/setting/portrait/personalData/sports";
import travel from "pages/setting/portrait/personalData/travel";
import video from "pages/setting/portrait/personalData/video";
import game from "pages/setting/portrait/personalData/game";
