<template>
  <div class="setting-other">
    <ul class="setting-tab">
      <router-link v-for="menu in menus" tag="li" :to='{name:menu.name,query:query,params:params}'>{{menu.title}}
      </router-link>
    </ul>
    <transition name="fade" mode="out-in">
      <router-view class="pd-20"></router-view>
    </transition>
    <router-link class="back-btn" tag="span" to="/setting/people"><i class="iconfont icon-fanhui"></i> 返回</router-link>
  </div>
</template>

<script type="text/javascript">
  export default {
    name: "setting2",
    data(){
      return {
        params: this.$route.params,
        query: this.$route.query,
        menus: [
          {title: '现实人物画像', name: 'realisticPortrait'},
          {title: '虚拟人物画像', name: 'virtualPortrait'}
        ]
      }
    }
  }
</script>

<style lang="scss" scoped>
  .pd-20{
    padding: 20px;
  }
  .setting-other{
    position: relative;
  }
  .setting-tab {
    color: #939fc2;
    font-size: 14px;
    display: flex;
    line-height: 26px;
    font-weight: bold;
    box-sizing: border-box;
    position: relative;
    &:after {
       content: "";
       display: block;
       width: 100%;
       height: 2px;
       position: absolute;
       background-color: #102456;
       left: 0;
       bottom: 0;
     }
    li {
      padding: 0 10px 5px;
      margin-right: 10px;
      cursor: pointer;
      position: relative;
    }
    & li:hover {
      color: #fff;
      &:after {
         content: "";
         display: block;
         width: 100%;
         height: 2px;
         position: absolute;
         background-color: #fed61e;
         left: 0;
         bottom: 0;
         z-index: 2;
      }
    }
    .active {
      box-sizing: border-box;
      font-size: 20px;
      color: #fff;
      &:after {
         content: "";
         display: block;
         width: 100%;
         height: 2px;
         position: absolute;
         background-color: #fed61e;
         left: 0;
         bottom: 0;
         z-index: 2;
      }
    }
  }

  .back-btn {
    position: absolute;
    top: -6px;
    right: 20px;
    height: 14px;
    /*font-family: MicrosoftYaHei;*/
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 40px;
    letter-spacing: 0px;
    color: #939fc2;
    cursor: pointer;
  .iconfont{
    font-size: 14px;
    color:#939fc2;
  }
  }

</style>



